import { parseWhereClause as arcgisParseWhereClause } from "@arcgis/core/core/sql";

/**
 * Parses a where clause, see `@arcgis/core/core/sql` for usage.
 * @param clause
 * @param fields
 */
export function parseWhereClause(clause: string, fields: __esri.FieldsIndex): Promise<__esri.WhereClause> {
  return arcgisParseWhereClause(clause, fields);
}
