import ParameterValue from "@arcgis/core/rest/support/ParameterValue";

import { LayerUrlFilter } from "analysis-ui-schema";

import { isEmptyDataItem } from "../common";

import { AnalysisHistoryItem, historyVersion } from "./interfaces";

/**
 * Fixes the raster tool category by updating old items from "rasterfunction" to "rastertool"
 * and also adds a version number if it was missing.
 * @param {AnalysisHistoryItem} historyItem the item to update
 * @returns {AnalysisHistoryItem} the updated history item
 */
function fixRasterToolsCategory(historyItem: AnalysisHistoryItem): AnalysisHistoryItem {
  const updatedHistoryItem = historyItem;
  if (historyItem.category === "rasterfunction") {
    updatedHistoryItem.category = "tools";
  }
  return updatedHistoryItem;
}

/**
 * Converts gpDataType results into a partially blank parameterValue object.
 * Makes an assumption that it is a feature record set layer because at this point, non layer results
 * were not supported.
 * @param {AnalysisHistoryItem} historyItem the history item to update
 * @returns {AnalysisHistoryItem} the updated history item
 */
function convertResultsToParameterValues(historyItem: AnalysisHistoryItem): AnalysisHistoryItem {
  const updatedHistoryItem = historyItem;
  updatedHistoryItem.results = updatedHistoryItem.results
    ?.filter((result: LayerUrlFilter | ParameterValue) => isEmptyDataItem(result) === false)
    .map((result: LayerUrlFilter | ParameterValue) => {
      return "value" in result === true && typeof result === "object"
        ? (result )
        : new ParameterValue({
            paramName: "",
            dataType: "feature-record-set-layer",
            value: result 
          });
    });
  return updatedHistoryItem;
}

/**
 * Converts items from 1.0.0 to 1.1.0
 * @param {AnalysisHistoryItem} historyItem the item to update
 * @returns {AnalysisHistoryItem} the updated history item
 */
// eslint-disable-next-line  @typescript-eslint/naming-convention
function convert_1_0_0_to_1_1_0(historyItem: AnalysisHistoryItem): AnalysisHistoryItem {
  return fixRasterToolsCategory(historyItem);
}

/**
 * Converts items from 1.1.0 to 1.2.0
 * @param {AnalysisHistoryItem} historyItem the item to update
 * @returns {AnalysisHistoryItem} the updated history item
 */
// eslint-disable-next-line  @typescript-eslint/naming-convention
function convert_1_1_0_to_1_2_0(historyItem: AnalysisHistoryItem): AnalysisHistoryItem {
  return convertResultsToParameterValues(historyItem);
}

/**
 * Updates a historyItem when loaded into the app to the current version
 * @param {AnalysisHistoryItem} historyItem the item to update
 * @param historyList
 * @param currentHistoryVersion
 * @returns {AnalysisHistoryItem} the updated history item
 */
export function convertHistorySpec(
  historyList: AnalysisHistoryItem[],
  currentHistoryVersion?: string
): AnalysisHistoryItem[] {
  let updatedHistoryList = historyList;
  updatedHistoryList = historyList.map((historyItem: AnalysisHistoryItem) => {
    let versionUpdatedTo = currentHistoryVersion;
    let updatedHistoryItem = historyItem;
    // this version comes from the top level of the history object and is passed in from the studio components store.
    while (versionUpdatedTo !== historyVersion) {
      // note that this version does not need to be returned as we set the version to latest in the studio components store
      // when we save the history list.
      switch (versionUpdatedTo) {
        case "1.0.0":
        case undefined:
          updatedHistoryItem = convert_1_0_0_to_1_1_0(historyItem);
          versionUpdatedTo = "1.1.0";
          break;
        case "1.1.0":
          updatedHistoryItem = convert_1_1_0_to_1_2_0(historyItem);
          versionUpdatedTo = "1.2.0";
          break;
        case "1.2.0":
        default:
          versionUpdatedTo = historyVersion;
          break;
      }
    }
    // return for map, not function.
    return updatedHistoryItem;
  });
  return updatedHistoryList;
}
