import Query from "@arcgis/core/rest/support/Query";
import FeatureLayer from "@arcgis/core/layers/FeatureLayer";
import MapView from "@arcgis/core/views/MapView";
import * as promiseUtils from "@arcgis/core/core/promiseUtils";

import { isEmptyDataItem } from "../common";
import { throwError } from "../t9n/errors";
import { ErrorKeywords } from "../errors/interfaces";

/**
 *
 * Returns the total number of features satisfying the layer's configuration/filters.
 * If a layer has timeExtent and layer.useViewTime is true, it would return the layer features
 * within the view extent. When useViewTime is false, it provides the count based on the layer's timeExtent.
 * See https://developers.arcgis.com/javascript/latest/api-reference/esri-layers-FeatureLayer.html#useViewTime.
 * @param layer
 * @param view
 */
export const queryCount = async (layer: FeatureLayer, view: MapView): Promise<number> => {
  // TODO: Look into memoizing this function. Found that serializing the entire function arguments is too slow and blocks the UI
  // Enable cacheHint so server can cache the same query request
  // using simple where clause for passing definitionExpression
  // TODO: reuse the robust combineFilters available in charts
  // https://devtopia.esri.com/WebGIS/arcgis-charts/blob/master/packages/arcgis-charts-shared-utils/src/query-parser/index.ts#L24
  // Analysis tools UI can add runtime filter as well so using combineFilter we can do layer.definitionExpression AND analysis filter.
  const props: ConstructorParameters<typeof Query>[0] | undefined = {
    cacheHint: true,
    where: isEmptyDataItem(layer.definitionExpression) === false ? layer.definitionExpression : "1=1"
  };
  if (isEmptyDataItem(layer.timeInfo) === false) {
    if (layer.useViewTime === true && isEmptyDataItem(view?.timeExtent) === false) {
      props.timeExtent = view.timeExtent;
    } else {
      props.timeExtent = layer.timeExtent;
    }
  }
  const query = new Query(props);
  const count = await layer.queryFeatureCount(query);
  return count;
};

export type AbortControllerKeyType = string | number | Date;

export const abortControllerMap = new Map<AbortControllerKeyType, AbortController>();
export type Features = __esri.Graphic[] | number[];

/**
 *  Fetches features from the layer, according to the passed query
 * @param props
 * @param {FeatureLayer} props.layer layer passed to query
 * @param {__esri.Query} props.query the query object which is being queried
 * @param {boolean} props.returnUniqueIdsOnly if true only objectIds are returned as per the query
 * @param {AbortControllerKeyType} props.abortKey abort key to abort the query request
 */
export async function queryFeatures(props: {
  layer?: FeatureLayer;
  query: __esri.Query;
  returnUniqueIdsOnly?: boolean;
  abortKey?: AbortControllerKeyType;
}): Promise<Features> {
  const { layer, query, returnUniqueIdsOnly, abortKey } = props;

  let graphics: Features = [];

  if (layer !== undefined) {
    try {
      if (returnUniqueIdsOnly === true) {
        graphics = await layer.queryObjectIds(query);
      } else {
        const featuresSet = await layer.queryFeatures(query, {
          signal: abortKey !== undefined ? abortControllerMap.get(abortKey)?.signal : undefined
        });
        graphics = featuresSet.features;
      }
    } catch (e) {
      // Not throwing errors when we're aborting previous queries
      if (promiseUtils.isAbortError(e) === false) {
        throwError(ErrorKeywords.QueryFeaturesError);
      }
    }
  }
  return graphics;
}

/**
 * Aborts all existing queries and initializes a controller for new ones. The queries are associated
 * to a unique key to avoid cancelling queries for other charts.
 * @param key The key pointing to the abort controller to execute
 */
export function abortPendingQueries(key?: AbortControllerKeyType): void {
  if (key !== undefined) {
    // Looking for an existing controller
    const currentAbortController = abortControllerMap.get(key);

    // If it exists, we call an abort on it
    if (currentAbortController !== undefined) currentAbortController.abort();

    // Setting a new controller for the next round of queries
    abortControllerMap.set(key, new AbortController());
  }
}
