import { omit, pick, set } from "lodash-es";

import { AnalysisToolDataItem, AnalysisToolUIParam, SerializationType } from "analysis-ui-schema";

import { isEmptyDataItem } from "../common";

import { AbstractSerializer } from "./interfaces";
import { WithKeys } from "./utils";

/**
 * Returns a string representation of the values in the provided input object with a space delimiter.
 * It's important to note that this method will not work for objects with nested objects.
 * @param {Record<string, AnalysisToolDataItem>} value - the value to be serialized
 * @returns {string} a serialized string
 * @example
 * const value = { a: 1, b: 2, c: 3 };
 * serializeSequentialValues(value); // "1 2 3"
 */
function serializeSequentialValues(value: Record<string, AnalysisToolDataItem>): string {
  const valueList = Object.values(value).map((item) => (!isEmptyDataItem(item) ? `${item}` : ""));
  return valueList.join(" ").trim();
}

/**
 * Serializes an array of objects into an array of sequential values
 * @example
 *  const value = [{ a: 1, b: 2, c: 3 }, { a: 4, b: 5, c: 6 }, {}];
 *  const keys = ["a", "b"];
 *  serializeSequentialArrayValues(value, keys); // ["1 2", "4 5"]
 * @param {AnalysisToolDataItem[]} value - the value to be serialized
 * @param {string[]} keys - the keys of the objects in the array to be serialized
 * @returns {string[]} the array of sequential values
 */
function serializeSequentialArrayValues(value: AnalysisToolDataItem[], keys: string[]): string[] {
  let serializedValue: string[] = [];

  const isValueArray = Array.isArray(value);
  const hasKeys = keys !== undefined && keys.length > 0;

  const shouldSerialize = isValueArray && hasKeys;
  if (shouldSerialize) {
    value.forEach((item) => {
      if (typeof item === "object") {
        const serializedItem = pick(item, keys) as Record<string, AnalysisToolDataItem>;

        // Make sure the serialized item is not empty
        if (!isEmptyDataItem(serializedItem)) {
          serializedValue.push(serializeSequentialValues(serializedItem));
        }
      }
    });
  }
  // this handles the case when the value array is already serialized.
  else if (
    isValueArray === true &&
    hasKeys === false &&
    value.length > 0 &&
    value.every((item) => typeof item === "string")
  ) {
    serializedValue = value as string[];
  }
  return serializedValue;
}

/**
 * Returns an object representation of the provided value
 * If the value is an object, it will be returned as is.
 * If the value is an array, it will be returned as an object with the provided name as the key.
 * @param {Record<string, AnalysisToolDataItem> | AnalysisToolDataItem[]} value - the object or array to be serialized
 * @param {string[]} keys - the keys of the object to be serialized
 * @param {string} name - the name of the object to be serialized
 * @returns {Record<string, AnalysisToolDataItem>} the serialized object or original value
 */
function serializeArrayValues(
  value: Record<string, AnalysisToolDataItem> | AnalysisToolDataItem[],
  keys?: string[],
  name?: string
): Record<string, AnalysisToolDataItem> | AnalysisToolDataItem[] {
  const serializedValue = {};

  const isValueArray = Array.isArray(value);
  const hasKeys = keys !== undefined && keys.length > 0;
  const hasName = name !== undefined;

  const shouldSerialize = isValueArray && hasKeys && hasName;
  if (shouldSerialize) {
    let serializedValues: AnalysisToolDataItem[] = [];

    value.forEach((item) => {
      if (typeof item === "object") {
        const serializedItem = pick(item, keys);
        serializedValues = [...serializedValues, ...Object.values(serializedItem)];
      }
    });

    set(serializedValue, name, serializedValues);
  }

  return shouldSerialize ? serializedValue : value;
}

/**
 * Wraps a value in an Array
 * @example
 *  const value = { a: 1, b: 2, c: 3 };
 *  serializeWrapInArray(value); // [{ a: 1, b: 2, c: 3 }]
 *  const value = [a: 1, b: 2, c: 3 ];
 *  serializeWrapInArray(value); // [a: 1, b: 2, c: 3 ]
 * @param {AnalysisToolDataItem[]} value - the value to be wrapped in an array
 * @returns {AnalysisToolDataItem[]} the array of values
 */
function serializeWrapInArray(value: Record<string, AnalysisToolDataItem>): AnalysisToolDataItem[] {
  return isEmptyDataItem(value) === false ? [value].flat() : [];
}

/**
 * The default serializer. This performs no serialization and returns the value as is.
 * This default is defined so that every serialization type has a serializer.
 * @category Private
 */
class DefaultSerializer extends AbstractSerializer<
  Record<string, AnalysisToolDataItem> | AnalysisToolDataItem[],
  Record<string, AnalysisToolDataItem> | AnalysisToolDataItem[]
> {
  /**
   * Serialization method that returns the value as is.
   * @param {Record<string, AnalysisToolDataItem> | AnalysisToolDataItem[]} value - the value to be serialized
   * @returns {Record<string, AnalysisToolDataItem> | AnalysisToolDataItem[]} the value as is
   */
  serialize(
    value: Record<string, AnalysisToolDataItem> | AnalysisToolDataItem[]
  ): Record<string, AnalysisToolDataItem> | AnalysisToolDataItem[] {
    return value;
  }
}

/**
 * Serialization class for sequential values
 * @category Private
 *
 * contains the following methods:
 * 1. serialize: serializes the value
 * 2. prepare: prepares the value to be serialized
 * 3. postProcess: post process the serialized value
 *
 * These three methods can be added to a Serializer interface and implemented by all the serializers,
 * though currently as we have only one serializer, this adds unnecessary overhead. The pattern can also
 * be extended to a "builder" pattern, where a list of prepare, serialize and postProcess methods can be
 * added to a builder to create a serializer. This would allow us to handle much more complex serialization
 * scenarios.
 *
 * There are also two private properties:
 * 1. toSerializeKeys: the keys of the value to be serialized
 * 2. value: the value to be serialized
 *
 * These are set in the prepare method and used in the postProcess method. Preventing the need to recalculate them
 * in the postProcess method.
 * @example <caption>Example of a sequential value with serializationParameterName</caption>
 * const serializer = new SequentialValuesSerializer();
 * const value = { a: 1, b: 2, c: { d: 3, e: 4 } };
 * const paramConfig = { serializationParameterName: "c" };
 * const serializedValue = serializer.serialize(serializer.prepare(value, paramConfig));
 * // serializedValue = "3 4"
 * const fullyProcessedValue = serializer.postProcess(serializedValue, paramConfig);
 * // fullyProcessedValue = { a: 1, b: 2, c: "3 4" }
 * @example <caption>Example of a sequential value with serializationParameterName and serializationKeys</caption>
 * const serializer = new SequentialValuesSerializer();
 * const value = { a: 1, b: 2, c: { d: 3, e: 4 } };
 * const paramConfig = { serializationParameterName: "c", serializationKeys: ["e", "d"] };
 * const serializedValue = serializer.serialize(serializer.prepare(value, paramConfig));
 * // serializedValue = "4 3"
 * const fullyProcessedValue = serializer.postProcess(serializedValue, paramConfig);
 * // fullyProcessedValue = { a: 1, b: 2, c: "4 3" }
 * @example <caption>Example of a sequential value with serializationKeys</caption>
 * const serializer = new SequentialValuesSerializer();
 * const value = { a: 1, b: 2, c: 3 };
 * const paramConfig = { serializationKeys: ["c", "b", "a"] };
 * const serializedValue = serializer.serialize(serializer.prepare(value, paramConfig));
 * // serializedValue = "3 2 1"
 * const fullyProcessedValue = serializer.postProcess(serializedValue, paramConfig);
 * // fullyProcessedValue = "3 2 1"
 */
class SequentialValuesSerializer extends WithKeys(AbstractSerializer<Record<string, AnalysisToolDataItem>, string>) {
  /**
   * The un-modified, original value to be serialized
   * This property is set in the prepare method and used in the postProcess method. This is done
   * to prevent the need to re-pass the value to the postProcess method
   * @category Private
   */
  private originalValue: Record<string, AnalysisToolDataItem>;

  /**
   * Serializes the value, this method should be called after the prepare method with the value returned by the prepare method
   * as the argument
   * @param {Record<string, AnalysisToolDataItem>} value the value to be serialized
   * @returns {string} the serialized value
   */
  serialize(value: Record<string, AnalysisToolDataItem>): string {
    return serializeSequentialValues(value);
  }

  /**
   * Prepares the data to be serialized based on the parameter configuration
   * @param {Record<string, AnalysisToolDataItem>} value the original value to be serialized
   * @param {AnalysisToolUIParam} paramConfig the parameter configuration, containing information about how to serialize the value
   * @returns {Record<string, AnalysisToolDataItem>} the modified value to be serialized
   */
  prepare(
    value: Record<string, AnalysisToolDataItem>,
    paramConfig: AnalysisToolUIParam
  ): Record<string, AnalysisToolDataItem> {
    this.originalValue = value;
    this.getKeys(value, paramConfig);

    let toSerializeValue = value;
    toSerializeValue = pick(value, this.toSerializeKeys);

    return toSerializeValue;
  }

  /**
   *
   * @param {string} value the serialized value as returned by the serialize method
   * @param {AnalysisToolUIParam} paramConfig the parameter configuration, containing information about how to serialize the value
   * @returns {Record<string, AnalysisToolDataItem> | string} the post processed value
   */
  postProcess(value: string, paramConfig: AnalysisToolUIParam): Record<string, AnalysisToolDataItem> | string {
    let fullyProcessedValue: Record<string, AnalysisToolDataItem> | string = value;

    // If serializationParameterName is defined, if so, we have serialized a subset of the original value
    // and we need to add the serialized value back to the original value
    if (paramConfig.serializationParameterName !== undefined) {
      // if serializationParameterName is defined, then the serialized value will be
      const unSerializedSubset = omit(this.originalValue, this.toSerializeKeys);
      // using lodash set to add the serialized value by the serialization name
      set(unSerializedSubset, paramConfig.serializationParameterName, value);
      fullyProcessedValue = unSerializedSubset;
    }

    return fullyProcessedValue;
  }
}

/**
 * Serialization class for arrays with sequential values
 * @category Private
 * @example <caption>Example of a sequential array with serializationParameterName</caption>
 * const serializer = new SequentialValuesSerializer();
 * const value = [{"mergingAttributesField":"COLOR","toBeMergedField":"color","outputType":"MATCH"},{"mergingAttributesField":"Value","outputType":"RENAME","mergedRenameField":"New value"}]
 * const paramConfig = {
 *   "serializationType": "arrayWithSequentialValues",
 *   "serializationParameterName": "mergingAttributes",
 *   "serializationKeys": ["mergingAttributesField", "operationType",
 *     "mergedRenameField", "toBeMergedField"],
 * };
 * const serializedValue = serializer.serialize(serializer.prepare(paramConfig));
 * // serializedValue = ["COLOR Match color","Value Rename New value"]
 */
class SequentialValueArraySerializer extends WithKeys(AbstractSerializer<AnalysisToolDataItem[], string[]>) {
  /**
   * Main method for serializing the value
   * @param {AnalysisToolDataItem[]} value the value to be serialized
   * @returns {string[]} the serialized value
   */
  serialize(value: AnalysisToolDataItem[]): string[] {
    return serializeSequentialArrayValues(value, this.toSerializeKeys);
  }

  /**
   * Prepares the data to be serialized based on the parameter configuration
   * @param {AnalysisToolDataItem[]} value the original value to be serialized
   * @param {AnalysisToolUIParam} paramConfig the parameter configuration, containing information about how to serialize the value
   * @returns {AnalysisToolDataItem[]} the un-modified value to be serialized
   */
  prepare(value: AnalysisToolDataItem[], paramConfig: AnalysisToolUIParam): AnalysisToolDataItem[] {
    this.getKeys(value, paramConfig);
    return value;
  }
}

/**
 * Serialization class for sequential arrays
 * @category Private
 *
 * contains the following methods:
 * 1. serialize: serializes the value
 * 2. prepare: prepares serializer parameters for serialization
 *
 * There are also two private properties:
 * 1. toSerializeKeys: the keys of the value to be serialized
 * 2. toSerializeName: the name to assgin to
 *
 * These are set in the prepare method and used in the serialize method.
 * @example <caption>Example of a sequential array with serializationParameterName</caption>
 * const serializer = new ArrayValuesSerializer();
 * const value = [url: "aaaaa", url: "bbbbb"];
 * const paramConfig = { serializationKeys: ["url"], serializationParameterName: "urls" };
 * const serializedValue = serializer.serialize(serializer.prepare(paramConfig));
 * // serializedValue = {urls: ["aaaaa", "bbbbb"] }
 */
class ArrayValuesSerializer extends WithKeys(
  AbstractSerializer<
    Record<string, AnalysisToolDataItem> | AnalysisToolDataItem[],
    Record<string, AnalysisToolDataItem> | AnalysisToolDataItem[]
  >
) {
  /**
   * The name to be serialized to
   * This property is set in the prepare method and used in the postProcess method
   * @category Private
   */
  private toSerializeName: string | undefined;

  /**
   * Serializes the value, this method should be called after the prepare method
   * @param {Record<string, AnalysisToolDataItem>} value the value to be serialized
   * @returns {string | Record<string, AnalysisToolDataItem>} the serialized value
   */
  serialize(
    value: Record<string, AnalysisToolDataItem> | AnalysisToolDataItem[]
  ): Record<string, AnalysisToolDataItem> | AnalysisToolDataItem[] {
    return serializeArrayValues(value, this.toSerializeKeys, this.toSerializeName);
  }

  /**
   * Prepares the data to be serialized based on the parameter configuration
   * @param {Record<string, AnalysisToolDataItem> | AnalysisToolDataItem[]} value the original value to be serialized
   * @param {AnalysisToolUIParam} paramConfig the parameter configuration, containing information about how to serialize the value
   * @returns {Record<string, AnalysisToolDataItem> | AnalysisToolDataItem[]} the provided value
   */
  prepare(value: Record<string, AnalysisToolDataItem> | AnalysisToolDataItem[], paramConfig: AnalysisToolUIParam) {
    this.toSerializeName = paramConfig.serializationParameterName;
    this.getKeys(value, paramConfig);

    return value;
  }
}

/**
 * Serialization class to serialize the received type as strinsg
 * @category Private
 * @example <caption>Example of a string serializer with serializationParameterName</caption>
 * const serializer = new StringValueSerializer();
 * const value = [{"mergingAttributesField":"COLOR","toBeMergedField":"color","outputType":"MATCH"},{"mergingAttributesField":"Value","outputType":"RENAME","mergedRenameField":"New value"}]
 * const paramConfig = {
 *   "serializationType": "string",
 *   "serializationParameterName": "mergingAttributes"
 * };
 * const serializedValue = serializer.serialize(serializer.prepare(paramConfig));
 * // serializedValue = '[{"mergingAttributesField":"COLOR","toBeMergedField":"color","outputType":"MATCH"},{"mergingAttributesField":"Value","outputType":"RENAME","mergedRenameField":"New value"}]'
 */
class StringValueSerializer extends WithKeys(AbstractSerializer<AnalysisToolDataItem[], string>) {
  /**
   * Main method for serializing the value
   * @param {AnalysisToolDataItem[]} value the value to be serialized
   * @returns {string[]} the serialized value
   */
  serialize(value: AnalysisToolDataItem[]): string {
    return typeof value === "string" ? value : JSON.stringify(value);
  }
}

/**
 * Serialization class for wrapping value in an array
 * @category Private
 *
 * contains the following methods:
 * 1. serialize: serializes the value
 * 2. prepare: prepares the value to be serialized
 * 3. postProcess: post process the serialized value
 *
 *
 * There are also two private properties:
 * 1. toSerializeKeys: the keys of the value to be serialized
 * 2. value: the value to be serialized
 *
 * These are set in the prepare method and used in the postProcess method. Preventing the need to recalculate them
 * in the postProcess method.
 * @example <caption>Example of a wrap value in an array</caption>
 * const serializer = new WrapInArraySerializer();
 * const value = { a: 1, b: 2, c: 3 };
 * const paramConfig = undefined;
 * const serializedValue = serializer.serialize(serializer.prepare(value, paramConfig));
 * // serializedValue = [{ a: 1, b: 2, c: 3 }]
 * const fullyProcessedValue = serializer.postProcess(serializedValue, paramConfig);
 * // fullyProcessedValue = [{ a: 1, b: 2, c: 3 }]
 */
class WrapInArraySerializer extends WithKeys(
  AbstractSerializer<Record<string, AnalysisToolDataItem>, AnalysisToolDataItem[]>
) {
  /**
   * Serializes the value, this method should be called after the prepare method with the value returned by the prepare method
   * as the argument
   * @param {Record<string, AnalysisToolDataItem>} value the value to be serialized
   * @returns {string} the serialized value
   */
  serialize(value: Record<string, AnalysisToolDataItem>): AnalysisToolDataItem[] {
    return serializeWrapInArray(value);
  }
}

/**
 * Map of classes that can be used to serialize a value
 * Use unknown for the input and output types as the input and output types are different for each serializer
 */
export const serializers: { [K in SerializationType]: new () => AbstractSerializer<unknown, unknown> } = {
  [SerializationType.sequentialValues]: SequentialValuesSerializer,
  [SerializationType.sequentialValueArray]: SequentialValueArraySerializer,
  [SerializationType.arrayValues]: ArrayValuesSerializer,
  [SerializationType.string]: StringValueSerializer,
  [SerializationType.wrapInAnArray]: WrapInArraySerializer,
  [SerializationType.default]: DefaultSerializer
} as const;
