import { AnalysisToolUIParam } from "analysis-ui-schema";

import { AbstractSerializer } from "./interfaces";

/**
 * Helper to check if a value is an object or array
 * @param {unknown} value - the value to check
 * @returns {boolean} true if the value is an object or array
 */
const isNonNullObject = (value: unknown): value is Record<string, unknown> =>
  typeof value === "object" && value !== null;

/**
 * Mixin for AbstractSerializer that manages serialization keys for serializers classes
 * @param {typeof AbstractSerializer} Base - the base class to be extended
 * @returns {typeof AbstractSerializerWithKeys} the extended class
 */
export function WithKeys<Input, Output>(Base: typeof AbstractSerializer<Input, Output>) {
  abstract class AbstractSerializerWithKeys extends Base {
    /**
     * The keys of the value to be serialized
     * This property is set in the prepare method and used in the postProcess method
     * @category Private
     */
    toSerializeKeys: string[];

    /**
     * A common method to get the keys of the object to be serialized.
     * This method also updates the toSerializeKeys property
     * @param {Input} value - the value that will be serialized and contains the keys
     * @param {AnalysisToolUIParam} paramConfig - the configuration for the parameter
     * @returns {string[]} the keys of the object to be serialized
     */
    getKeys(value: Input, paramConfig: AnalysisToolUIParam): string[] {
      let keys: string[] = [];

      if (Array.isArray(paramConfig.serializationKeys) && paramConfig.serializationKeys.length > 0) {
        // Always use the serialization keys if they are provided
        keys = paramConfig.serializationKeys;
      } else if (isNonNullObject(value)) {
        const allValueKeys: string[] = Array.isArray(value)
          ? value.flatMap((arrayValue) =>
              isNonNullObject(arrayValue) && !Array.isArray(arrayValue) ? Object.keys(arrayValue) : []
            )
          : Object.keys(value);

        // make sure they are unique
        keys = [...new Set(allValueKeys)];
      }

      this.toSerializeKeys = keys;

      return keys;
    }
  }

  return AbstractSerializerWithKeys;
}
