import {
  AnalysisParamModel,
  AnalysisToolData,
  AnalysisToolDataItem,
  AnalysisToolInfo,
  AnalysisToolUI,
  AnalysisToolUIParam,
  LocaleItem,
  OutputName,
  ParamViewModelKey
} from "analysis-ui-schema";

import { ParamGroupDataTypeKey, SelectedLayersKey } from "../interfaces";
import { ArcgisWebAnalysisError } from "../errors";

import { AnalysisToolErrorState } from "./error";
/**
 * Abstract interface for dataset field information.
 */
export interface FieldInfo {
  name: string;
  alias: string;
  type: string;
  description?: string;
}

interface Options {
  signal: AbortSignal;
}

/**
 * Abstract interface for custom datasets used in Analysis tools.
 */
export interface Dataset {
  id: string;
  name: string;
  description?: string;
  getFields: (options?: Options) => Promise<FieldInfo[]>;
}

/**
 * Allowed items in the global data object
 */
export type GlobalValues =
  | AnalysisToolDataItem
  | __esri.Layer
  | __esri.Layer[]
  | SupportedLayer
  | SupportedLayer[]
  | Dataset
  | Dataset[]
  | __esri.MapView
  | OutputName["itemProperties"];

/**
 * Data structure used to store and manage tool parameters.
 */
export interface AnalysisToolGlobalData {
  mapView?: __esri.MapView;
  mapLayers?: SupportedLayer[];
  analysisSessionLayers?: __esri.FeatureLayer[];
  analysisDatasets?: Dataset[];
  [key: string]: GlobalValues;
}

/**
 * Known constants that translate to calculated values at job submission time.
 */
export const CalculatedParameterValueKeys = {
  CurrentTime: "CALCULATE_CURRENT_TIME"
} as const;

/**
 * Names of known constants that translated to calculated values at job submission time.
 */
export type CalculatedParameterValueKeys =
  | (typeof CalculatedParameterValueKeys)[keyof typeof CalculatedParameterValueKeys];

// TODO: route layer import is not working
// TODO: shapefile(.zip) import is not working
/**
 * Supported layer types that can be used as an input to an analysis tool.
 */
export type SupportedLayer =
  | __esri.FeatureLayer
  | __esri.TileLayer
  | __esri.ImageryLayer
  | __esri.ImageryTileLayer
  | __esri.CSVLayer
  | __esri.KMLLayer
  | (__esri.MapNotesLayer & { url: string })
  | __esri.MapImageLayer;
// added the url property to mapnotes layer just to avoid changes in type errors in code where url is expected on all supported layer types.
// actually there we should have used FeatureLayer type not SupportedLayer.

/**
 * Supported Layer with parent property
 * FIXME: parent property is not in the JS API typings for Layer even though it is in its code
 * so using this approach.
 */
export type SupportedLayerWithParent = SupportedLayer & { parent: __esri.Layer };

/**
 * Supported layer types that can show up as a result of an analysis tool.
 */
export type SupportedResult =
  | __esri.FeatureLayer
  | __esri.TileLayer
  | __esri.ImageryLayer
  | __esri.ImageryTileLayer
  | __esri.RouteLayer
  | __esri.GroupLayer
  | __esri.PortalItem;

/**
 * Helper type for the schema of the process info messages
 */
export type ProcessInfoMessage = {
  messageCode: string;
  message: string | string[];
  params: Record<string, string>;
  style: string;
};

/**
 *
 * Type for the analysis tool UI assets object.
 *
 */
export interface AnalysisToolUIAssets {
  /**
   * Analysis tool REST json that describes the tool.
   */
  toolJson: AnalysisToolInfo;
  /**
   * Optional analysis tool ui json that is used override toolJson to provide richer UI.
   *
   */
  toolUIJson?: AnalysisToolUI;
  /**
   * Optional tool ui labels and related strings for displaying the tool ui. It goes along wth toolUIJson.
   */
  toolT9n?: LocaleItem;
  /**
   * Strings loaded for the tool
   *
   */
  toolUIStrings?: LocaleItem;
  /**
   * Optional tool help file path (currently an HTML file) to show popup parameter help.
   */
  toolHelpFilePath?: string;
}

/**
 *
 * Type for the analysis tool assets object. This includes the toolJson from REST tool information along with the other properties from analysis tool ui assets.
 *
 */
export interface AnalysisToolJsons extends AnalysisToolUIAssets {
  /**
   * REST tool json is required property of this assets object.
   *
   */
  toolJson: AnalysisToolInfo;
}

/**
 * These are keys to be removed from the tool data before submitting the job.
 * Meaning these will not be included in the ui only params at all (and therefore
 * not shown in the history parameter list UI)
 */
export const DefaultUIOnlyParamKeys = {
  /**
   * These are full keys that will be removed. They will only be removed on
   * a full match.
   */
  ExcludedUIOnlyParamKeys: ["itemProperties", "userSettings"],
  /**
   * These are partial keys that will be removed. They will be removed if
   * the key contains the partial key (substring match).
   */
  ExcludedUIOnlyParamPartialKeys: [SelectedLayersKey],
  /**
   * These are full keys that will always be present in the ui only params.
   * Meaning if they are not present in the tool data, they will be added
   * even if they are undefined. These also cannot be removed using the
   * ExcludedUIOnlyParamKeys or ExcludedUIOnlyParamPartialKeys.
   */
  AlwaysIncludedUIOnlyParamKeys: ["saveResultIn"]
} as const;

/**
 * These are keys that will be removed from the base ui only params before
 * displaying the history parameter list UI. Meaning these will not be displayed,
 * but will not be removed from the base ui only params.
 *
 * This also means since these are removed from the base ui only params, any keys
 * that are added to the base ui only params will be displayed in history
 * unless they are also added to this list.
 */
export const DefaultUIOnlyParamKeysForDisplay = {
  /**
   * These are full keys that will be removed. They will only be removed on
   * a full match with a key in the base ui only params.
   */
  ExcludedUIOnlyParamKeysForUI: [...DefaultUIOnlyParamKeys.ExcludedUIOnlyParamKeys, ParamViewModelKey],
  /**
   * These are partial keys that will be removed. They will be removed if
   * the key contains the partial key (substring match) with a key in the
   * base ui only params.
   */
  ExcludedUIOnlyParamPartialKeysForUI: [...DefaultUIOnlyParamKeys.ExcludedUIOnlyParamPartialKeys, ParamGroupDataTypeKey]
};

export interface PrepJobParamsForSubmissionOptions {
  /**
   * The tool data to be submitted.
   *
   * These are the REST parameters that are submitted to the server.
   * This data can be serialized or replaced depending on the parameter's
   * related tool UI json.
   */
  jobParams: AnalysisToolData;
  /**
   * All of the data that is currently in the tool.
   *
   * This contains all data for the tool's current state. Including, the Parameter
   * View Model, UIOnlyParams, and the job params present in the UI. This data is
   * only used when a parameter is renamed.
   */
  toolData: AnalysisToolData | undefined;
  /**
   * The tool UI json that is used to determine how to serialize the tool data.
   *
   * This contains all the information about the tool's UI. However it is not
   * reflective of the current state of the tool. It is only used to determine
   * if and how to serialize the tool data.
   */
  toolUIJson: AnalysisToolUIParam[];
  /**
   * The name of the tool that is being submitted.
   *
   * This is passed to determine if the tool requires special handling. While not explicitly
   * required, it allows for more robust guardrails nearing the 2023 R2 release.
   */
  toolName: string | undefined;
  /**
   * Tool information and tool param information.
   */
  toolJSON?: AnalysisToolInfo;
  /**
   * A flag to determine the portal's "isPortal" property. This is used to determine
   * if the output names should be formatted or not.
   * @default false
   */
  isPortal?: boolean | undefined;
}

export interface FilterAndRenameParamsOptions {
  /**
   * The job params/data to be filtered and renamed.
   *
   * These are the REST parameters that are submitted to the server and the base/starting
   * data that will be filtered and renamed.
   */
  jobParams: AnalysisToolData | undefined;
  /**
   * The tool UI only parameters (all tool data that is not submitted to the server).
   *
   * This contains parameters and data that may be renamed to replace a job param.
   */
  UIOnlyParams: AnalysisToolData | undefined;
  /**
   * The tool UI parameter view model that is used to determine how to filter and rename the tool data.
   */
  toolUIParamModel: AnalysisParamModel | undefined;
  /**
   * A flag to determine if the parameter's submission name or the parameter's original name should be used.
   *
   * Submission name should be used when submitting the job, but original name should be used when displaying
   * the parameter in the UI (as it maps back to the tool UI json).
   */
  useSubmissionName?: boolean;
}

/**
 * ConvertJobParamsToToolDataOptions interface type for convertJobParamsToToolData function argument
 */
export interface ConvertJobParamsToToolDataOptions {
  /**
   * The job params/data to be converted to tool data.
   *
   * These are the REST parameters that are submitted to the server and the base/starting
   * data that will be filtered and renamed.
   */
  jobParams: AnalysisToolData | undefined;
  /**
   * The tool UI only parameters (all tool data that is not submitted to the server).
   *
   * This contains parameters and data that may be renamed to replace a job param.
   */
  uiOnlyParams: AnalysisToolData;
  /**
   * The name of the tool used
   */
  toolName: string;
  /**
   * List of available layers for analysis in the current map
   */
  availableMapLayers: SupportedLayer[];
  /**
   * Tool information and tool param information.
   */
  toolJSON: AnalysisToolInfo;
  /**
   * A flag to determine if the tool is a web tool or not.
   */
  isWebTool: boolean;
}

/**
 * The returned results of the convertJobParamsToToolData function
 */
export interface ConvertJobParamsToToolDataReturnType {
  /**
   * The tool data that was converted from the job params
   */
  convertedJobParams: AnalysisToolGlobalData;

  /**
   * A list of layers that were not found in the map and need to be added
   */
  layers: SupportedLayer[];

  /**
   * The error state representing any errors that were encountered
   * while converting the job params to tool data that should be
   * displayed in the UI
   */
  resultingErrorState: AnalysisToolErrorState;
}

/**
 * Return type for convert job layers to map layers function
 */
export interface ConvertJobLayersToMapLayersReturnType {
  /**
   * Layers already in the map
   */
  existingLayers: SupportedLayer[];

  /**
   * Layers that need to be added to the map
   * There was an attempt to load these layers but no
   * gaurantee that they were successfully loaded
   */
  layersToAdd: SupportedLayer[];

  /**
   * Any errors that were encountered while converting the layers
   */
  errors: ArcgisWebAnalysisError<unknown>[];
}

/**
 * The capabilities property of a geoprocessing service
 */
export type GPServiceCapabilities = Array<"Uploads" | "">;
