import PortalUser from "@arcgis/core/portal/PortalUser";

/**
 * Helper to check if the logged in user has NA license
 * @param {PortalUser} user the logged in user
 * @returns {boolean} true/false
 */
export function hasNAPrivilege(user?: PortalUser): boolean {
  const privilegesArray = user?.sourceJSON.privileges as string[] | undefined;
  return privilegesArray !== undefined && privilegesArray.includes("premium:user:networkanalysis");
}

/**
 * Checks if the logged in user has Geoenrichment privileges
 * @param {PortalUser} user the logged in user
 * @returns {boolean} true/false
 */
export function hasGeoEnrichmentPrivilege(user?: PortalUser): boolean {
  const privilegesArray = user?.sourceJSON.privileges as string[] | undefined;
  return privilegesArray !== undefined && privilegesArray.includes("premium:user:geoenrichment");
}

/**
 * Checks if the logged in user has the runWebTools privilege
 * @param {PortalUser} user the logged in user
 * @returns {boolean} true/false
 */
export function hasRunWebToolPrivilege(user?: PortalUser): boolean {
  const privilegesArray = user?.sourceJSON.privileges as string[] | undefined;
  return privilegesArray !== undefined && privilegesArray.includes("portal:user:runWebTool");
}
