import Portal from "@arcgis/core/portal/Portal";
import esriId from "@arcgis/core/identity/IdentityManager";

import { FederatedServer } from "../interfaces";
import { isImageryLayer, isImageryTileLayer } from "../tools";
import { SupportedLayer } from "../tools/interfaces";
import { ErrorKeywords } from "../errors/interfaces";
import { throwError } from "../t9n/errors";
import { isEmptyDataItem, request } from "../common";

/**
 * This function checks whether a layer is allowed for analysis
 * @param {SupportedLayer} layer mapLayer to be checked
 * @returns {boolean} true if layer is allowed for analysis and false otherwise
 */
export function checkLayerAllowAnalysis(layer: SupportedLayer): boolean {
  let allowedAnalysis = true;
  if (isImageryLayer(layer) || isImageryTileLayer(layer)) {
    // check allowAnalysis flag from sourceJSON
    if (layer.sourceJSON?.allowAnalysis === false) {
      allowedAnalysis = false;
    }

    const isLayerHosted = layer.portalItem?.typeKeywords?.includes("Hosted Service") ?? false;
    if (isLayerHosted) {
      // if the hosted imagery layer is within the same org as user,
      // then it is considered as collocated service which analysis tool could directly access the source data.
      allowedAnalysis = layer.portalItem.isOrgItem ?? false ? true : allowedAnalysis;
    }
  }
  return allowedAnalysis;
}

/**
 * This function filters a list of layers by checking allow analysis property and returns list of layers allowed for analysis
 * @param {SupportedLayer[]} layers mapLayers to be filtered
 * @returns {SupportedLayer[]} list of layers filtered allow for analysis
 */
export function filterLayersAllowAnalysis(layers: SupportedLayer[]): SupportedLayer[] {
  return layers.filter((layer) => checkLayerAllowAnalysis(layer));
}

/**
 * Get federated servers list from ArcGIS Enterprise deployed on WindowsLinux or Kubernetes
 * @param {Portal} portal portal instance
 * @returns {Promise<FederatedServer[]>} promise of the federated servers list from Enterprise
 */
export async function getFederatedServers(portal: Portal): Promise<FederatedServer[]> {
  const { id, isPortal, restUrl, user } = portal;
  let federatedServers: FederatedServer[] = [];
  if (isPortal && !isEmptyDataItem(id) && !isEmptyDataItem(restUrl) && !isEmptyDataItem(user)) {
    const url = restUrl + "/portals/" + id + "/servers";
    const credential = esriId.findCredential(url);

    let token;
    if (credential !== undefined) {
      token = credential.token;
    }

    const options = {
      query: {
        token: token,
        f: "json"
      }
    };

    try {
      const response = await request(url, options);
      federatedServers = response?.data?.servers ?? [];
    } catch (e) {
      await throwError(ErrorKeywords.NetworkError);
    }
  }
  return federatedServers;
}
