import { flattenObject } from "../common";

import { NestedObject, srCategories } from "./spatial-references";

export type { NestedObject } from "./spatial-references";
export { srCategories };

let wkidMap: Map<number, string> | undefined = undefined;

/**
 * Returns a flat map of all the spatial references, using WKID as the key
 * @returns {Map<number, string>} the map of spatial references
 */
export function getWkidMap(): Map<number, string> {
  if (wkidMap === undefined) {
    wkidMap = new Map();

    const flatWkids = flattenObject(srCategories);

    Object.keys(flatWkids).forEach((key) => {
      wkidMap?.set(Number(key), flatWkids[key]);
    });
  }
  return wkidMap;
}

/**
 * Returns matching spatial reference description for given WKID
 * @param {number} wkid the WKID of the spatial reference
 * @returns {string} the description of the spatial reference (if found)
 */
export function getSpatialReferenceDescription(wkid: number): string | undefined {
  if (wkidMap === undefined) wkidMap = getWkidMap();

  return wkidMap.get(wkid);
}

/**
 * Returns list of matching spatial references for a given search string
 * @param {string} searchString the search stringFields
 * @returns {NestedObject} the list of matching spatial references
 */
export function filterSpatialReferences(searchString: string): NestedObject {
  if (wkidMap === undefined) wkidMap = getWkidMap();
  // This regex contains all special characters and will let us replace them with an escapedd value in the regex
  const specialCharacters = /[\!\#\$\%\^\&\*\)\(\+\=\.\<\>\{\}\[\]\:\;\'\"\|\~\`\_\-]/g;

  const regexString = searchString.replace(specialCharacters, "\\$&");

  // use u for unicode to include special characters
  // use i to ignore case
  // use g for a global search
  const regexPattern = new RegExp(regexString, "igu");

  const matches: NestedObject = {};

  wkidMap.forEach((value, key) => {
    regexPattern.lastIndex = 0;
    if (regexPattern.test(value) || regexPattern.test(key.toString())) {
      matches[String(key)] = value;
    }
  });

  return matches;
}
