import { throwError } from "../t9n/errors";

/**
 * Writes an item to session storage
 * @param { string} key sessionStorage Key
 * @param {string} value What you want to write (stringified)
 */
export async function writeToSessionStorage(key: string, value: string) {
  try {
    window.sessionStorage.setItem(key, value);
  } catch (exception) {
    if (exception.name === "QuotaExceededError" || exception.name === "NS_ERROR_DOM_QUOTA_REACHED") {
      // If the error is that session storage is full we should handle it and translate it
      await throwError("fullSessionStorage", "writeToSessionStorage");
    } else {
      // otherwise generic errors should still bubble up.
      throw exception;
    }
  }
}

/**
 * Removes an item from local storage
 * @param {string} key sessionStorage Key
 */
export function removeFromSessionStorage(key: string) {
  window.sessionStorage.removeItem(key);
}

/**
 * Reads and returns an item in session storage.
 * @param {string} key the key to read from session storage.
 * @returns {any} the value in session storage for that key.
 */
export function readFromSessionStorage(key: string): any {
  return window.sessionStorage.getItem(key);
}

/**
 * Determine if local storage is available
 * https://developer.mozilla.org/en-US/docs/Web/API/Web_Storage_API/Using_the_Web_Storage_API#testing_for_availability
 * @returns {boolean} is the local storage available
 */
export function isSessionStorageAvailable(): boolean {
  let response = false;
  try {
    const testKey = "__sessionStorageAvailableTest__";
    window.sessionStorage.setItem(testKey, testKey);
    window.sessionStorage.removeItem(testKey);
    response = true;
  } catch (exception) {
    response =
      exception instanceof DOMException &&
      (exception.code === 22 ||
        exception.code === 1014 ||
        exception.name === "QuotaExceededError" ||
        exception.name === "NS_ERROR_DOM_QUOTA_REACHED") &&
      window.sessionStorage !== null &&
      window.sessionStorage !== undefined &&
      window.sessionStorage.length !== 0;
  }
  return response;
}
