import { UpdateSavedResourceProps } from "./interfaces";
import { addOrUpdateItemResource, readItemResource } from "./item-resource";
import {
  isSessionStorageAvailable,
  readFromSessionStorage,
  removeFromSessionStorage,
  writeToSessionStorage
} from "./session-storage";

/**
 * Dynamically loads a resource by doing: webMap ?? sessionStorage ?? undefined
 * @param {string} path the path to the resource, example: `"analysis/history/file1.json"`
 * @param {__esri.PortalItem | undefined} portalItem the portalItem to use, used for reading from the item resources
 * @returns {Promise<T | undefined>} returns the data stored in the webMap or sessionStorage, or undefined if neither exist
 */
export async function loadSavedResource<T>(path: string, portalItem?: __esri.PortalItem): Promise<T | undefined> {
  let itemData;
  // First check if there is any data stored in the item resources.
  const loadItemResource = portalItem !== undefined && portalItem !== null;
  try {
    if (loadItemResource) {
      itemData = await readItemResource<T>(path, portalItem);
    }
    // Second if there is no data in the item resources, check session storage.
    if (itemData === undefined && isSessionStorageAvailable()) {
      const sessionStorageData = readFromSessionStorage(path);
      if (sessionStorageData !== null) {
        itemData = JSON.parse(sessionStorageData);
      }
    }
  } catch (e) {
    // Bubbles up any error that occured from downstream methods
    throw e;
  }
  return itemData;
}

/**
 * Updates an entry in an item resource.
 * @param {UpdateSavedResourceProps} props the properties to use: storageKey: string, portalItem: __esri.PortalItem, resource: T, version: string.
 * @returns {Promise<string>} the resource name that was written.
 */
async function updateItemResource<T>(props: UpdateSavedResourceProps<T>): Promise<string> {
  const { portalItem, path, resource } = props;
  let writtenName = "";

  if (portalItem !== undefined) {
    try {
      await addOrUpdateItemResource({
        data: resource,
        portalItem,
        path
      });

      writtenName = path;
    } catch (e) {
      // Bubbles up any error that occured from downstream methods
      throw e;
    }
  }
  return writtenName;
}

/**
 * Updates an entry in session storage with the provided data
 * @param {Pick<UpdateSavedResourceProps<T>, "resource" | "storageKey" | "version">} props the properties that are picked from UpdateSavedResourceProps
 * @returns {{Promise<string>}} whether or not the operation was successful.
 */
async function updateSessionStorage<T>(
  props: Pick<UpdateSavedResourceProps<T>, "resource" | "storageKey" | "version">
): Promise<string> {
  const { resource, storageKey } = props;
  let writtenName = "";
  if (isSessionStorageAvailable()) {
    try {
      removeFromSessionStorage(storageKey);
      await writeToSessionStorage(storageKey, JSON.stringify(resource));
      writtenName = storageKey;
    } catch (e) {
      // Bubbles up any error that occured from downstream methods
      throw e;
    }
  }
  return writtenName;
}

/**
 * Updates a resource that may have already been saved, or adds a new one. `webMap ?? sessionStorage ?? undefined`
 * @param {UpdateSavedResourceProps} props the properties to use: storageKey: string, portalItem: __esri.PortalItem, resource: AnalysisHistoryItem[], version: string.
 * @returns {Promise<boolean>} whether or not the operation was successful.
 */
export async function updateSavedResource<T>(props: UpdateSavedResourceProps<T>): Promise<string> {
  const { resource, storageKey, version, onlyWriteToSessionStorage } = props;
  let writtenName = "";
  const writeToItemResource = props.portalItem !== undefined && onlyWriteToSessionStorage === false;
  try {
    if (writeToItemResource) {
      writtenName = await updateItemResource<T>(props);
    } else {
      writtenName = await updateSessionStorage<T>({ resource, storageKey, version });
    }
  } catch (e) {
    // Bubbles up any error that occured from downstream methods
    throw e;
  }
  return writtenName;
}
