import { AnalysisToolDataItem } from "./analysis-tool-data";
import { AnalysisToolParam } from "./analysis-tool-param";
import { AnalysisToolUIParam } from "./analysis-tool-ui-param";

/**
 * Here are some examples of rules
 *
 * This is an equals rule:
 * ```json
 * {
 *   ruleType: "equals",
 *   valueA: "$[Data].value",
 *   valueB: "10",
 *   destination: "$[Data].destination",
 *   effectParams: {
 *     color: "red"
 *   }
 * }
 * ```
 * This will update `destination` with the specified `effectParams` if `valueA`  equals `valueB`.
 * In this case, if `valueA` is 10 and `valueB` is 10, `destination.color` will be set to `red`.
 *
 * This is a set rule:
 * ```json
 * {
 *   ruleType: "set",
 *   value: "10",
 *   destination: "$[Data].numberInput",
 *   property: "min"
 * }
 * ```
 * This will not perform any evaluation. It will indiscriminately set `destination.property` to `value`.
 * In this case `$[Data].numberInput.min` will be set to `10`.
 * Note that the set rule does not use effectParams.
 *
 * This is an expression rule:
 * ```json
 * {
 * ruleType: "expression",
 *   expression: "$[Data].value > 10",
 *   destination: "$[Data].destination",
 *   effectParams: {
 *     color: "red"
 *   }
 * }
 * ```
 * This will evaluate an sql expression and update `destination` with the specified `effectParams` if the expression evaluates to true.
 * In this case, if `value` is greater than 10, `destination.color` will be set to `red`.
 */

// TODO: remove any throws spec error in JSONEditor
export type AnalysisToolRuleEffectParams = AnalysisToolUIParam | AnalysisToolParam | null | any;

/**
 * The types that a rule can have.
 */
export const AnalysisRuleType = {
  Equals: "equals",
  Set: "set",
  Expression: "expression"
} as const;
export type AnalysisRuleType = (typeof AnalysisRuleType)[keyof typeof AnalysisRuleType];

export interface AnalysisExpressionOperator {
  value: AnalysisOperator;
  type: number;
  precedence: number;
}

/**
 * The operators that can be used to evaluate an expression.
 */
export const AnalysisOperator = {
  And: "AND",
  Or: "OR",
  Not: "NOT",
  In: "IN",
  NotIn: "NOT IN",
  Equals: "=",
  GreaterThan: ">",
  LessThan: "<",
  GreaterThanOrEquals: ">=",
  LessThanOrEquals: "<=",
  Like: "LIKE",
  NotLike: "NOT LIKE"
} as const;
export type AnalysisOperator = (typeof AnalysisOperator)[keyof typeof AnalysisOperator];

/**
 * A rule can either be an equals, set, or expression.
 * If the condition of the rule is met then the effect is applied.
 */
export type AnalysisToolUIRule = AnalysisEqualsRule | AnalysisSetRule | AnalysisExpressionRule;

/**
 * The base type for all rules.
 */
export interface AnalysisBaseRule {
  /**
   * The type of rule.
   */
  ruleType: AnalysisRuleType;
  // TODO: extract destination and effectParams into separate interface and make an array
  /**
   * A name, or list of names, of the parameters to apply the rules to.
   */
  destination: string | string[];
}

/**
 * Checks equality, if it is equal then the effect is applied.
 */
export interface AnalysisEqualsRule extends AnalysisBaseRule {
  /**
   * The type of the rule. Always equals.
   */
  ruleType: "equals";
  /**
   * The first value to compare.
   */
  valueA: AnalysisToolDataItem;
  /**
   * The second value to compare.
   */
  valueB: AnalysisToolDataItem;
  /**
   * The parameters to apply to the rule.
   */
  effectParams: AnalysisToolRuleEffectParams;
}

/**
 * Indiscriminately sets the value of source to the property on the destination.
 */
export interface AnalysisSetRule extends AnalysisBaseRule {
  /**
   * The type of the rule. Always set.
   */
  ruleType: "set";
  /**
   * The value to set.
   */
  value: AnalysisToolDataItem;
  /**
   * The property name to set on the destination.
   */
  property: string;
}

/**
 * An expression rule is a rule that is evaluated and if it is true then the effect is applied.
 */
export interface AnalysisExpressionRule extends AnalysisBaseRule {
  /**
   * The type of the rule. Always expression.
   */
  ruleType: "expression";
  /**
   * expression follows SQL where clause expression syntax
   */
  expression: string;
  /**
   * The parameters to apply to the rule.
   */
  effectParams: AnalysisToolRuleEffectParams;
}

/**
 * The format that an expression must take.
 * It must be [ expressionItem, operator, expressionItem ]
 * this will look something like this: [ x, ">", y ] or [ x, ">=", 32 ] and so on.
 */
export type AnalysisExpression = [
  AnalysisToolDataItem | AnalysisExpression,
  AnalysisExpressionOperator,
  AnalysisToolDataItem | AnalysisExpression
];

/**
 * Rule used for run time validation
 */
export interface AnalysisValidationRule {
  expression: string;
  errorKey: string;
}
