import { AnalysisToolUITravelMode } from "./analysis-tool-ui-travel-mode";
import { AnalysisExpression } from "./analysis-tool-ui-rule";
import { AnalysisRasterToolContext, AnalysisToolContext } from "./analysis-tool-context";
import { AnalysisToolUIParam } from "./analysis-tool-ui-param";
import { AnalysisToolParam } from "./analysis-tool-param";
import { AnalysisRFxDataItem } from "./analysis-rfx-ui";

import {
  GPBoolean,
  GPDataFile,
  GPDouble,
  GPFeatureRecordSetLayer,
  GPLinearUnit,
  GPMultiValue,
  GPRasterDataLayer,
  GPRecordSet,
  GPString
} from "./geoprocessing-rest-data-types";
import { GPQuery } from "./query-builder-types";

export type AnalysisToolPrimitiveDataItem =
  | AnalysisToolContext
  | AnalysisRasterToolContext
  | GPBoolean
  | GPString
  | GPDouble
  | GPFeatureRecordSetLayer
  | GPRecordSet
  | GPRasterDataLayer
  | GPDataFile
  | GPMultiValue
  | GPLinearUnit
  | GPQuery
  | AnalysisToolUITravelMode
  | AnalysisExpression
  | undefined
  | null;

export type AnalysisToolDataItem =
  | AnalysisToolPrimitiveDataItem
  | AnalysisRFxDataItem
  | { [key: string]: AnalysisToolPrimitiveDataItem } // key-value
  | { [key: string]: AnalysisToolPrimitiveDataItem }[]; // value table

/**
 * enum name for property where the parameter view model is stored
 */
export const ParamViewModelKey = "parameterViewModel";

/**
 * JSON representation of a parameter that Analysis tool would submit for execution.
 */
export interface AnalysisToolData {
  [key: string]: AnalysisToolDataItem;
  context?: AnalysisToolContext;
  [ParamViewModelKey]?: AnalysisParamModel;
}

/**
 * The combined properties used in analysis tool to keep track of the tool form.
 */
export interface AnalysisToolFormData {
  jobParams?: AnalysisToolData;
  toolUiParameters?: AnalysisToolData;
}

/**
 * Keys from a tool ui parameter to pick for saving to the parameter view model
 * These are the keys available in the history details to access info about the parameter state
 * For future extending, a property just needs to be added to this array to include it
 */
export const UIParamModelKeys = [
  "choiceListLabels",
  "label",
  "visible",
  "componentName",
  "serializationType",
  "serializationParameterName",
  "serializationKeys",
  "uiParameterInfoSubSet",
  "submissionName"
] as const;

/**
 * Keys from a tool parameter to pick for saving to the parameter view model
 * These are the keys available in the history details to access info about the parameter state
 * For future extending, a property just needs to be added to this array to include it
 */
export const ToolParamModelKeys = ["dataType", "choiceList", "parameterType"] as const;

/**
 * Intermediary types for cleanliness
 */
type AnalysisUIParamModel = Pick<AnalysisToolUIParam, (typeof UIParamModelKeys)[number]>;
type AnalysisToolParamModel = Pick<AnalysisToolParam, (typeof ToolParamModelKeys)[number]>;

/**
 * Parameter view model containing a subset of UI parameters and tool parameters keyed on parameter name
 * Saved with the uiOnlyParameters to pass more information about the UI at job submission to the history details
 * The tool component performs a `pick` from the above array or properties, providing a "snapshot" for the ui
 * Examples:
 *  { "name": "pointLayer", "label": "$inputPointLayer", "componentName": "analysis-layer-input", "layerFilterType": ["feature_point"] }
 *  -> { pointLayer: { label: "$inputPointLayer", componentName: "analysis-layer-input" }}
 *
 *  { "name": "ringType", "visible": false, "choiceList": ["Disks", "Rings"], "choiceListLabels": { "Disks": "$disks", "Rings": "$rings" }}
 *  -> { ringType: { choiceList: ["Disks", "Rings"], choiceListLabels:  "Disks": "$disks", "Rings": "$rings" }, visible: false }}
 */
export type AnalysisParamModel = Record<string, Partial<AnalysisUIParamModel & AnalysisToolParamModel>>;

/**
 * The serialization method to use when getting the value of an input.
 * This can be extended to support other serialization methods.
 */
export const SerializationType = {
  default: "default",
  sequentialValues: "sequentialValues",
  sequentialValueArray: "sequentialValueArray",
  arrayValues: "arrayValues",
  string: "stringValue",
  wrapInAnArray: "wrapInAnArray"
} as const;
export type SerializationType = (typeof SerializationType)[keyof typeof SerializationType];
