import { ArcgisWebAnalysisError } from "../errors";

import { Cache, CacheType, RequestCache, TypeParameter } from "./interfaces";

const cache: Cache = {};
const requestCache: RequestCache = {};

/**
 * Allows memory caching of requests which are expected to be called multiple times
 * @param requester - function which fetches the data to be cached
 * @param key - unique global name for this cache type - see {@link CacheType}
 * @param id - unique id for this result inside this particular cache
 */
export async function fromCache(
  requester: () => Promise<TypeParameter>,
  key: CacheType,
  id: string
): Promise<TypeParameter | undefined> {
  const cachedValue = cache[key]?.[id];
  if (cachedValue !== undefined) {
    return cachedValue;
  }

  const requestedCacheId = requestCache[key];
  if (requestedCacheId === undefined || requestedCacheId === null) {
    requestCache[key] = {} as Record<string, Promise<TypeParameter>>;
  }

  const requestedCacheValue = requestCache[key][id];
  if (requestedCacheValue === undefined || requestedCacheValue === null) {
    requestCache[key][id] = requester();
  }

  const cachedId = cache[key];
  if (cachedId === undefined || cachedId === null) {
    cache[key] = {} as Record<string, TypeParameter>;
  }

  let result;

  try {
    result = await requestCache[key][id];
    cache[key][id] = result;
  } catch (error) {
    throw new ArcgisWebAnalysisError("CacheFetchError", error);
  }

  return result;
}

/**
 * Checks for a value in the cache and returns if there
 * If missing, no requests will be performed
 * @param key - unique global name for this cache type - see {@link CacheType}
 * @param id - unique id for this result inside this particular cache, will clear all entries if not provided
 * @example
 * inCache("serviceInfo", "uniqueitemid");
 */
export function inCache(key: CacheType, id: string): TypeParameter | undefined {
  return cache?.[key]?.[id];
}

/**
 * Clear all cache entries
 * @example
 * clearAllCache();
 */
export const clearAllCaches = () => {
  Object.keys(cache).forEach((key) => {
    delete cache[key];
  });
  Object.keys(requestCache).forEach((key) => {
    delete requestCache[key];
  });
};
