import Layer from "@arcgis/core/layers/Layer";
import PortalItem from "@arcgis/core/portal/PortalItem";

import { AnalysisToolInfo, AnalysisToolParam, AnalysisToolParamDirection, GPDataType } from "analysis-ui-schema";

import { isEmptyDataItem } from "../common";

import { getLayerObject } from "./gp-layer";

/**
 * Returns the result parameters from toolJSON.
 * @param {AnalysisToolInfo} toolJSON passed toolJson input
 * @returns {string[]} list of result params
 */
export function getResultParams(toolJSON: AnalysisToolInfo): string[] {
  const { parameters } = toolJSON;
  const resultParams = parameters
    ?.filter((parameter: AnalysisToolParam) => parameter.direction === AnalysisToolParamDirection.Output)
    .map((parameter: AnalysisToolParam) => parameter.name);
  return resultParams ?? [];
}

/**
 * Returns true if the input is a portalItem of type feature service or image service, and false otherwise
 * @param {Layer | PortalItem} result the instance of layer or portal item
 * @returns {boolean} whether or nto the layer object should be created or we should use the portalItem
 */
export function shouldCreateLayerObject(result: Layer | PortalItem): boolean {
  return result instanceof PortalItem && (result.type === "Feature Service" || result.type === "Image Service");
}

/**
 * Returns true if the result is an instance of layer or is an instance of portalItem with either "Feature Service"
 * or "Image service" as the type
 * @param {Layer | PortalItem} result the instance of layer or portal item
 * @returns {result is __esri.PortalItem} boolean to add to map or not
 */
export function shouldAddToMap(result: Layer | PortalItem): boolean {
  return shouldCreateLayerObject(result) || result instanceof Layer;
}

/**
 * Gets the supported results from a gp result.
 * @param {GPDataType} gpResultValue the result to get the object of
 * @returns {Promise<Layer | PortalItem | undefined>} returns list of supported layer results
 */
export async function getSupportedResults(gpResultValue: GPDataType): Promise<Layer | PortalItem | undefined> {
  let result: Layer | PortalItem | undefined;
  if (
    typeof gpResultValue === "object" &&
    "itemId" in gpResultValue &&
    isEmptyDataItem(gpResultValue.itemId) === false
  ) {
    let portalItem = new PortalItem({
      id: gpResultValue.itemId
    });
    portalItem = await portalItem.load();
    if (shouldCreateLayerObject(portalItem) === true) {
      result = await getLayerObject(gpResultValue);
    } else {
      result = portalItem;
    }
  } else {
    result = await getLayerObject(gpResultValue);
  }
  return result;
}
