import PortalItemResource from "@arcgis/core/portal/PortalItemResource";

import { throwError } from "../t9n/errors";

import { PostItemResourceProps } from "./interfaces";

/**
 * Reads resources from the portalItemResource and returns them.
 * @param {string} path the path to the resource, should be a global const
 * @param {__esri.PortalItem} portalItem the portal item, this is used to get the url
 * @returns {Promise<T | undefined>} returns the object stored in the portalItem if there is one
 */
export async function readItemResource<T>(path: string, portalItem: __esri.PortalItem): Promise<T | undefined> {
  const portalItemResource = new PortalItemResource({
    portalItem,
    path
  });
  let portalItemResourceData = undefined;
  try {
    portalItemResourceData = await portalItemResource.fetch();
  } catch (e) {
    // Important note: this needs to stay no op so undefined can be returned properly to consuming functions.
    // no op
  }
  return portalItemResourceData;
}

/**
 * Stores an object in a portalItem's resources
 * @param {PostItemResourceProps} props the properties to use: storageKey: string, portalItem: __esri.PortalItem, storageItem: any
 * @returns {Promise<__esri.PortalItemResource>} the updated portalItemResource
 */
export async function addOrUpdateItemResource<T>(props: PostItemResourceProps<T>): Promise<__esri.PortalItemResource> {
  const { portalItem, data, path } = props;

  let portalItemResource = new PortalItemResource({
    portalItem,
    path
  });
  const resourceBlob = new Blob([JSON.stringify(data)], { type: "application/json" });
  const itemData = await readItemResource<T>(path as string, portalItem);
  try {
    if (itemData !== undefined) {
      portalItemResource = await portalItemResource.update(resourceBlob);
    } else {
      await portalItem.addResource(portalItemResource, resourceBlob);
    }
  } catch (e) {
    // TODO: add handling for generic errors not related to saving failing.
    // As of now, any error in saving to the webmap will be treated the same and will bubble up to parent.
    await throwError("cannotWriteToItemResource", "addOrUpdateItemResource");
  }

  return portalItemResource;
}
