import { get } from "lodash-es";
// eslint-disable-next-line @typescript-eslint/ban-ts-comment
// @ts-ignore - this is a temporary fix for the type definition. We are ignoring this error: TS7016 Could not find declaration file
import SqlWhereParser from "sql-where-parser";

import {
  AnalysisExpression,
  AnalysisExpressionOperator,
  AnalysisOperator,
  AnalysisToolData,
  AnalysisToolDataItem,
  AnalysisToolParam,
  AnalysisToolRuleEffectParams,
  AnalysisUIParamComponentProps,
  AnalysisUIParamProps
} from "analysis-ui-schema";

import { isEmptyDataItem } from "../common";
import {
  EvaluateProps,
  ParamGroupDataTypeKey,
  ParseValueProps,
  ProcessRuleProps,
  ProcessValidationRuleProps,
  SelectedLayersKey
} from "../interfaces";

export * from "./history-versioning";

function getValue<T>(object: T, key: string): AnalysisToolDataItem {
  return get(object, key);
}

const transformFns = ["map", "mapkv", "filter"] as const;

type Transform = (typeof transformFns)[number];

const isValidTransform = (t: string): t is Transform => transformFns.includes(t as Transform);

const arrayConcat = (acc: any[], curr: any) => [...acc, curr];

const toObject = (acc: any, curr: any) => ({ ...acc, ...curr });

const compose = (...fs: any[]) => fs.reduceRight((g, f) => (x: any) => g(f(x)));

const map = (transform: any) => (reducer: any) => (acc: any, curr: any) => reducer(acc, transform(curr));

const filter = (predicate: (i: any) => boolean) => (reducer: any) => (acc: any, curr: any) =>
  predicate(curr) ? reducer(acc, curr) : acc;

const DATA_PREFIX = "$[Data].";
const COMPONENT_PREFIX = "$[Component].";
const CURRENT_TOOL_PREFIX = "$[Component].currentTool.";
const TRANSFORM_SUFFIX = /(^.+)(?:\$\{)((?:\s*\w+(?:\s*\w+)+)(?:\s*;\s*\w+(?:\s*\w+)+)*\s*)(?:\}$)/;

/**
 * Parses a value from the analysis tool data if applicable, otherwise just returns the value unchanged.
 * @param {ParseValueProps} props
 * @returns {AnalysisToolDataItem} The parsed value.
 */
function parseValue(props: ParseValueProps): AnalysisToolDataItem {
  const { data, renderedParams } = props;
  const currentTool = props.currentTool ?? {};
  let value = props.value;
  let parsedValue: AnalysisToolDataItem;
  if (typeof value === "string") {
    let transforms: string | null = null;
    const match = value.match(TRANSFORM_SUFFIX);
    if (match !== null) {
      transforms = match[2];
      value = match[1];
    }
    if (value.includes(CURRENT_TOOL_PREFIX)) {
      parsedValue = getValue(currentTool, value.split(CURRENT_TOOL_PREFIX)[1]);
      parsedValue = isEmptyDataItem(parsedValue) ? undefined : parsedValue;
    } else if (value.includes(COMPONENT_PREFIX)) {
      parsedValue = getValue(renderedParams, value.split(COMPONENT_PREFIX)[1]);
      parsedValue = isEmptyDataItem(parsedValue) ? undefined : parsedValue;
    } else if (value.includes(DATA_PREFIX)) {
      parsedValue = getValue(data, value.split(DATA_PREFIX)[1]);
      parsedValue = isEmptyDataItem(parsedValue) ? undefined : parsedValue;
    } else {
      parsedValue = value;
    }
    if (transforms !== null) {
      let toArray = true;
      const fns: any[] = [];
      transforms.split(";").forEach((t) => {
        const [transform, ...args] = t.trim().split(/\s+/);
        if (isValidTransform(transform)) {
          if (transform === "map") {
            let fn;
            if (args.length === 1) {
              fn = (i: any) => i[args[0]];
            } else {
              fn = (i: any) => {
                const result: { [key: string]: any } = {};
                args.forEach((arg) => (result[arg] = i[arg]));
                return result;
              };
            }
            fns.unshift(map(fn));
          } else if (transform === "mapkv") {
            toArray = false;
            const [key, val] = args;
            fns.unshift(map((i: any) => ({ [i[key]]: i[val] })));
          } else if (transform === "filter") {
            const [attr, expr] = args;
            if (expr) {
              // TODO: evaluate expr, i.e: ...${filter fieldA > 20}
            } else {
              fns.unshift(filter((i: any) => i[attr] as boolean));
            }
          }
        } else {
          throw new Error(`Unknown transformation:${transform}`);
        }
      });
      if (toArray) {
        parsedValue = (<any>parsedValue).reduce(compose(...fns)(arrayConcat), []);
      } else {
        parsedValue = (<any>parsedValue).reduce(compose(...fns)(toObject), {});
      }
      parsedValue = isEmptyDataItem(parsedValue) ? undefined : parsedValue;
    }
  } else if (value === null) {
    parsedValue = undefined;
  } else {
    parsedValue = value;
  }
  return parsedValue;
}

/**
 * Recursively parses the effect params and if any values have changed, returns a new object.
 * @param {AnalysisToolRuleEffectParams} effectParams the effect params to parse
 * @param {AnalysisToolData} data the analysis tool data
 * @param {Record<string, HTMLElement>} renderedParams currently rendered components
 * @param {HTMLElement | undefined} currentTool the instance of analysis tool
 * @returns {AnalysisToolRuleEffectParams} the parsed effect params
 */
function parseEffectParams(
  effectParams: AnalysisToolRuleEffectParams,
  data: AnalysisToolData,
  renderedParams: Record<string, HTMLElement>,
  currentTool?: HTMLElement
): AnalysisToolRuleEffectParams {
  const isEffectParamsArray = Array.isArray(effectParams);
  const updatedEffectParams: AnalysisToolRuleEffectParams = isEffectParamsArray ? [] : {};

  // Track whether any values changed from the original effect params.
  let hasValueChanged = false;
  for (const [key, value] of Object.entries<AnalysisToolDataItem>(effectParams ?? {})) {
    if (typeof value === "object") {
      updatedEffectParams[key] = parseEffectParams(value, data, renderedParams, currentTool);
      if (updatedEffectParams[key] !== value) hasValueChanged = true;
    } else {
      const parsedValue = parseValue({ value, data, renderedParams, currentTool });

      const shouldMutateOriginal = isEffectParamsArray === true && parsedValue !== value;
      if (shouldMutateOriginal) {
        // To further narrow the scope of what will create a new object ref, if the
        // value is directly contained in an array, we don't use the new effect params
        // even if the value changed. This is often the case for "choiceList" and other
        // similar params and is the original behavior of the code. This is done to
        // prevent downstream component's from incorrectly emitting a change event.
        // If this requirement changes, this check can be removed and
        // the code should continue to use the new effect params if the value has changed.
        // But for now, we will mutate the original effect params
        effectParams[key] = parsedValue;
      } else {
        updatedEffectParams[key] = parsedValue;
      }
      if (shouldMutateOriginal === false && parsedValue !== value) hasValueChanged = true;
    }
  }

  return hasValueChanged ? updatedEffectParams : effectParams ?? undefined;
}

/**
 * Evaluates a single expression formatted as: `{a} {operator} {b}`.
 * @param {AnalysisToolDataItem} a The first term of the expression.
 * @param {AnalysisToolDataItem} b The last term of the expression.
 * @param {AnalysisExpressionOperator | string} operator The operator of the expression.
 * @returns {boolean} true if the expression evaluates to true, false otherwise.
 */
function evaluateExpression(
  a?: AnalysisToolDataItem,
  b?: AnalysisToolDataItem,
  operator?: AnalysisExpressionOperator
): boolean {
  let result = false;
  const bothBooleans = typeof a === "boolean" && typeof b === "boolean";
  const canPerformNumericComparisons = a !== undefined && b !== undefined && a !== null && b !== null;
  const canPatternMatch = typeof a === "string" && typeof b === "string";
  switch (operator?.value.toUpperCase()) {
    case AnalysisOperator.And:
      result = bothBooleans && a && b;
      break;
    case AnalysisOperator.Or:
      result = bothBooleans && (a || b);
      break;
    case AnalysisOperator.Equals:
      result = a === b;
      break;
    case AnalysisOperator.Not:
      result = a !== b;
      break;
    case AnalysisOperator.GreaterThan:
      result = canPerformNumericComparisons && a > b;
      break;
    case AnalysisOperator.LessThan:
      result = canPerformNumericComparisons && a < b;
      break;
    case AnalysisOperator.GreaterThanOrEquals:
      result = canPerformNumericComparisons && a >= b;
      break;
    case AnalysisOperator.LessThanOrEquals:
      result = canPerformNumericComparisons && a <= b;
      break;
    case AnalysisOperator.In:
      // If using the `IN` operator with numbers, convert to strings for consistent comparison
      result = Array.isArray(b) && b.map((val) => String(val)).includes(String(a));
      break;
    case AnalysisOperator.NotIn:
      result = Array.isArray(b) && !b.map((val) => String(val)).includes(String(a));
      break;
    case AnalysisOperator.Like:
      result = canPatternMatch && !!a.match(new RegExp(`^${b.replaceAll(/%+/g, ".*")}$`));
      break;
    case AnalysisOperator.NotLike:
      result = canPatternMatch && !a.match(new RegExp(`^${b.replaceAll(/%+/g, ".*")}$`));
      break;
    default:
      throw new Error(`Unknown operator:${operator}`);
  }
  return result;
}

/**
 * Evaluates the expressions array recursively.
 * @param {EvaluateProps} props
 * @returns {boolean} true if the expressions evaluate to true, false otherwise.
 */
function evaluate(props: EvaluateProps): boolean {
  const { expressions, data, renderedParams, currentTool } = props;
  if (expressions !== null) {
    const operator = expressions[1] as AnalysisExpressionOperator;
    let operandA = expressions[0];
    if (Array.isArray(operandA)) {
      operandA = evaluate({ expressions: operandA as AnalysisExpression[], data, renderedParams, currentTool });
    }

    let operandB = expressions[2];
    if (get(operandB, "value") === "LIKE" && operator.value === AnalysisOperator.Not && expressions[3] !== null) {
      // Adjust operator & operandB for `NOT LIKE` case
      operator.value = "NOT LIKE" as AnalysisOperator;
      operandB = expressions[3] as AnalysisExpression;
    } else if (get(operandB, "value") === "IN" && operator.value === AnalysisOperator.Not && expressions[3] !== null) {
      // Adjust operator & operandB for `NOT IN` case
      operator.value = "NOT IN" as AnalysisOperator;
      operandB = expressions[3] as AnalysisExpression;
    } else if (
      Array.isArray(operandB) &&
      operator.value !== AnalysisOperator.In &&
      operator.value !== AnalysisOperator.NotIn
    ) {
      // If the operator is IN, treat operandB as an array of values
      operandB = evaluate({ expressions: operandB as AnalysisExpression[], data, renderedParams, currentTool });
    }

    return evaluateExpression(
      parseValue({ value: operandA, data, renderedParams, currentTool }),
      parseValue({ value: operandB, data, renderedParams, currentTool }),
      operator
    );
  } else {
    // two return statements are unavoidable because of the recursion
    return false;
  }
}

type EffectParams =
  | (Omit<AnalysisUIParamProps, "name"> & AnalysisUIParamComponentProps)
  | Omit<AnalysisToolParam, "name">;

/**
 * Processes a rule and returns the effect to apply.
 * @param {ProcessRuleProps} props
 * @param props.rule rule to be processed
 * @param props.data tool data and state of the UI
 * @param props.renderedParams attributes of the rendered analysis components
 * @param {HTMLElement | undefined} props. the instance of analysis tool
 * @returns {AnalysisToolUIParam | AnalysisToolParam | undefined} the effect to apply
 */
export function processRule(props: ProcessRuleProps): EffectParams | undefined {
  const { rule, data, renderedParams, currentTool } = props;
  let effectParams: EffectParams | undefined;

  switch (rule.ruleType) {
    case "equals": {
      const { valueA, valueB } = rule;
      const parsedValueA = parseValue({ value: valueA, data, renderedParams, currentTool });
      const parsedValueB = parseValue({ value: valueB, data, renderedParams, currentTool });
      if (parsedValueA === parsedValueB) {
        effectParams = parseEffectParams(rule.effectParams, data, renderedParams, currentTool);
      }
      break;
    }
    case "set": {
      const parsedValue = parseValue({ value: rule.value, data, renderedParams, currentTool });

      if (typeof rule.property !== "string") {
        // Bad configuration, fail loudly
        throw new Error("`set` rule must have a string `property`");
      }
      effectParams = { [rule.property]: parsedValue } as EffectParams;
      break;
    }
    case "expression": {
      const parser = new SqlWhereParser();
      const sqlArray = parser.toArray(rule.expression) as AnalysisExpression[];
      const result = evaluate({ expressions: sqlArray, data, renderedParams, currentTool });
      if (result === true) {
        effectParams = parseEffectParams(rule.effectParams, data, renderedParams, currentTool);
      }
      break;
    }
    default:
      break;
  }
  return effectParams;
}

/**
 *
 * @param props
 */
export function processValidationRule(props: ProcessValidationRuleProps): boolean {
  const { rule, data, renderedParams, currentTool } = props;
  const parser = new SqlWhereParser();
  const sqlArray = parser.toArray(rule.expression) as AnalysisExpression[];
  const result = evaluate({ expressions: sqlArray, data, renderedParams, currentTool });
  return result;
}

/**
 * Generate a standard key to use for holding the selected layer in the data for a given input param
 * @param {paramName} the name of the param to use as a unique key prefix
 * @param paramName
 * @returns {string} the generated key
 */
export function generateSelectedLayersKey(paramName: string): string {
  return `${paramName}${SelectedLayersKey}`;
}

/**
 * Generate a standard key to use for holding the data type in the data for a given input param
 * @param {string} paramName the name of the param to use as a unique key prefix
 * @returns {string} the generated key
 */
export function generateParamGroupDataTypeKey(paramName: string): string {
  return `${paramName}${ParamGroupDataTypeKey}`;
}
