import { AnalysisToolUIParam } from "./analysis-tool-ui-param";
import { AnalysisToolUIRule, AnalysisValidationRule } from "./analysis-tool-ui-rule";
import { AnalysisToolUITravelModeInfo } from "./analysis-tool-ui-travel-mode-info";
import { AnalysisToolEnvironmentSettings } from "./analysis-tool-ui-environment-settings";

/**
 * Version of the current specification. Matches the package.json one.
 */
export const AnalysisToolUICurrentVersion = "0.1.0" as const;

export interface LocaleItem {
  [key: string]: string | LocaleItem;
}

/**
 * Gives a JSON representation of UI for an analysis tool.
 */
export interface AnalysisToolUI {
  /**
   * Name of the tool, e.g. create-drive-time-areas
   */
  toolName: string;
  /**
   * Version of the current UI schema specification.
   */
  version: typeof AnalysisToolUICurrentVersion;
  /**
   * Possible travel modes for network analysis.
   */
  travelModeInfo?: AnalysisToolUITravelModeInfo;
  /**
   * Filter travel mode types based on time , distance, other mode of travel, when defined only allows that mode.
   * when undefined ( default case) will allow all travel modes
   */
  filterTravelModeType?: "time" | "distance" | "other";
  /**
   * The travel mode choice list is dynamically generated
   * when true, includes the straight line option in that choice list
   */
  includeStraightLine?: boolean;
  /**
   * List of rules that are checked at job submit time
   */
  validationRules?: AnalysisValidationRule[];
  /**
   * List of rules that needs to be applied on the param UI.
   */
  rules?: AnalysisToolUIRule[];
  /**
   * Translated UI Strings used by the tool.
   */
  t9n?: LocaleItem;
  /**
   * Represents the UI for each of the parameters.
   */
  UIparameters: AnalysisToolUIParam[];
  /**
   * Object map to show/hide different environment settings.
   */
  environmentSettings?: AnalysisToolEnvironmentSettings;
}
