import uniqueValues from "@arcgis/core/smartMapping/statistics/uniqueValues";

import { intersectionBy, unionBy } from "lodash-es";

import { SupportedLayer, SupportedLayerWithParent } from "../tools/interfaces";
import { ArcgisWebAnalysisError } from "../errors";

import { FilterFieldsParams } from "./interfaces";

/**
 * @param {__esri.FeatureLayer | __esri.ImageryLayer | __esri.ImageryTileLayer} layer the layer to fetch from
 * @param {boolean} usePopupTemplateFieldsInfo use popup template or directly from the layer
 */
export async function fetchLayerFieldsInfo(
  layer: __esri.FeatureLayer | __esri.ImageryLayer | __esri.ImageryTileLayer,
  usePopupTemplateFieldsInfo: boolean
): Promise<__esri.Field[]> {
  await layer.load();
  let fieldsInfo: __esri.Field[] = [];
  if (layer.type === "imagery-tile" || layer.type === "imagery") {
    fieldsInfo =
      layer.type === "imagery-tile"
        ? layer.rasterInfo?.attributeTable?.fields
        : layer.serviceRasterInfo?.attributeTable?.fields;
    if (usePopupTemplateFieldsInfo === true && layer.rasterFields !== undefined) {
      const rasterFields = layer.rasterFields.map((field) => {
        field.name = field.name.replace(/^.*\.(.*)$/, "$1");
        return field;
      });
      const valueField = rasterFields.find((field) => field.name === "Raw");
      const valueFieldInfo = fieldsInfo.find((field) => field.name.toLowerCase() === "value");
      if (valueField !== undefined && valueFieldInfo !== undefined) {
        valueField.name = valueFieldInfo.name;
        valueField.alias = valueFieldInfo.alias;
        valueField.description = valueFieldInfo.description;
        valueField.type = valueFieldInfo.type;
      }
      fieldsInfo = intersectionBy(rasterFields, fieldsInfo, "name");
    }
  } else {
    fieldsInfo = [...layer.fields];
    if (usePopupTemplateFieldsInfo === true && layer?.popupTemplate?.fieldInfos !== undefined) {
      const popupFieldsInfo: __esri.Field[] = layer?.popupTemplate?.fieldInfos
        .filter((field) => layer.fieldsIndex.get(field.fieldName))
        .map((field) => {
          // converting the popupField to __esri.Field and then converting to IField
          const newField: __esri.Field = layer.fieldsIndex.get(field.fieldName);
          newField.alias = field.label;
          return newField;
        });
      // Replace field from feature layer with matching one from popup info based on name property
      // First array passed (popup info) has preference
      fieldsInfo = unionBy(popupFieldsInfo, fieldsInfo, "name");
    }
  }
  return fieldsInfo;
}

/**
 * Checks whether the given field has a valid numeric type.
 * @param {__esri.Field["type"]} fieldType
 * @returns {boolean} true or false based on the check
 */
export function isNumericField(fieldType: __esri.Field["type"]): boolean {
  const validNumericFields = ["small-integer", "integer", "big-integer", "single", "double", "long"];
  return validNumericFields.includes(fieldType);
}

/**
 * returns list of fields based on filter conditions
 * @param {FilterFieldParams} params list of fields along with filter conditions allowDateType, allowStringType
 * @returns {__esri.Field[]} returns filtered list of fields
 */
export function filterFields(params: FilterFieldsParams): __esri.Field[] {
  const { fields, allowDateType, allowStringType, allowOidType, allowNumericType, allowGeometryFields, layer } = params;
  return fields.filter((field: __esri.Field) => {
    let allowField = false;
    if (allowOidType === true && field.type === "oid") {
      allowField = true;
    } else if (
      allowNumericType === true &&
      isNumericField(field.type) === true &&
      (field.type !== "big-integer" || params.allowBigIntType === true)
    ) {
      if (
        [layer?.geometryFieldsInfo?.shapeAreaField, layer?.geometryFieldsInfo?.shapeLengthField].includes(field.name)
      ) {
        allowField = allowGeometryFields;
      } else {
        allowField = true;
      }
    } else if (allowDateType === true && field.type === "date") {
      allowField = true;
    } else if (allowStringType === true && field.type === "string") {
      allowField = true;
    }
    return allowField;
  });
}

/**
 * Returns the label for the layer input popover list item to represent the input layer in the UI.
 * @param {SupportedLayer | __esri.Sublayer} layer the layer instance for which label is constructed.
 * @returns {string} returns the label for the layer list item.
 * @category Private
 */
export function getLayerLabel(layer: SupportedLayer | __esri.Sublayer): string {
  // if the layer came from group layer we are looking at the parent , for sketch sub layer it has layer property
  const groupSubLayer = layer as SupportedLayerWithParent;
  const serviceTitle = groupSubLayer.parent?.title ?? undefined;
  return serviceTitle !== undefined ? `${serviceTitle} - ${layer.title}` : layer.title;
}

/**
 * Returns unique values
 * @param props - properties containing layer and fieldName
 * @param props.layer
 * @param props.view
 * @param props.fieldName
 */
export async function getUniqueValues(props: {
  layer: __esri.FeatureLayer;
  fieldName: string;
}): Promise<__esri.UniqueValuesResult> {
  const { layer, fieldName } = props;
  if (layer.type === "feature" && !layer.url) {
    layer.outFields = ["*"];
  }

  try {
    const result = await uniqueValues({
      layer,
      field: fieldName
    });
    return result;
  } catch (err) {
    throw new ArcgisWebAnalysisError("UniqueValuesRetrievalError", err);
  }
}
