import { isEqual, uniqWith } from "lodash-es";

import {
  AnalysisToolValidationParams,
  AnalysisValidationParams,
  AnalysisValidityState,
  MessageType,
  Url
} from "analysis-ui-schema";

import { ArcgisWebAnalysisError } from "../errors";
import { CLIENT_THROWN_ERROR } from "../t9n/errors/interfaces";
import { ErrorKeywords } from "../errors/interfaces";

import { getServiceName } from "./gp-layer";

export const ValidityState = {
  Valid: "valid",
  Info: "informative",
  Warning: "warning",
  Error: "error"
} as const;

export type ValidityState = (typeof ValidityState)[keyof typeof ValidityState];

/**
 * Parameter error interface
 * Used to track parameter-level errors
 */
export interface AnalysisParameterError {
  /**
   * A list of parameter errors
   */
  errors: AnalysisValidationParams[];

  /**
   * An optional map of nested parameter names to parameter errors
   * This allows for complex components to track their own errors
   * rather than relying on the tool
   */
  subParameterErrors?: {
    [key: string]: AnalysisParameterError;
  };
}

/**
 * Analysis tool error state interface
 * Used to track both tool-level and parameter-level errors
 * The location of the error is determined by where the error key
 * should be displayed in the UI
 */
export interface AnalysisToolErrorState {
  /**
   * Array of tool-level error keys
   * These are the errors that should be displayed at the top of the tool
   * including errors in analysis settings
   */
  toolAppErrorState: AnalysisToolValidationParams[];

  /**
   * Object mapping parameter names to the parameter's errors
   * These errors will be propagated for display in the input component's UI
   */
  parameterErrorState: {
    [key: string]: AnalysisParameterError;
  };
}

/**
 * Counterpart to the AnalysisValidityState interface
 * This interface is defined externally to this project
 */
interface RFxValidityState {
  valid: boolean;
  errorStrings: string[];
}

/**
 *  check if error was thrown from the client JS code (this project), the server, or third party
 * @param {Error | ArcgisWebAnalysisError | undefined} error error object to check
 * @returns {boolean} true if error was thrown from client code
 */
export function isClientThrownError(error: Error | ArcgisWebAnalysisError | undefined): boolean {
  /**
   *  FIXME: ideally (e instanceof ArcgisWebAnalysisError) would suffice than looking at e.details.type
   *  but that check is false here. It may be related to error was thrown in analysis-core
   *  and when stencil code catches it, may be the instance information is getting lost in transit.
   */
  return error !== undefined && "details" in error && error.details?.type === CLIENT_THROWN_ERROR;
}

/**
 *  @param {Error | ArcgisWebAnalysisError} error error object
 *  @param {string | undefined} defaultErrorString default error string to return if error is not thrown from client code
 *  @returns {string | undefined} error message (if available or fallback provided)
 */
export function extractErrorMessage(
  error?: Error | ArcgisWebAnalysisError,
  defaultErrorString?: string
): string | undefined {
  return isClientThrownError(error) ? error?.message : defaultErrorString;
}

/**
 * Helper to derrive a validity state from an array of AnalysisValidityState objects
 * @param {AnalysisValidityState[]} validityStates An array of AnalysisValidityState objects (one or more)
 * @returns {ValidityState} A ValidityState enum value
 */
export function getValidityStatus(validityStates: (AnalysisValidityState | RFxValidityState)[]): ValidityState {
  const areAllStatesValid = validityStates.every(({ valid }) => valid);
  return areAllStatesValid ? ValidityState.Valid : ValidityState.Error;
}

/**
 * Converts an error to an AnalysisValidationParams object
 * This is used to convert errors to a format that can be consumed by analysis input components
 * @param {object} params The params object
 * @param {Error} params.error The error to convert
 * @param {string | undefined} params.errorKey The error key to convert
 * @param {{ [key: string]: string }} params.variables Variables to pass to the error message. Defaults to an empty object
 * @param {ValidityState} params.type The validity state of the error. Defaults to ValidityState.Error
 * @param {string | undefined} params.message The error message to use if it cannot be extracted from the error
 * @returns {AnalysisValidationParams} An AnalysisValidationParams object
 */
export function convertErrorToAnalysisValidationParams({
  error,
  errorKey,
  message,
  variables = {},
  type = "error"
}: {
  error?: Error;
  errorKey?: string;
  variables?: { [key: string]: string };
  type?: MessageType;
  message?: string;
}): AnalysisValidationParams {
  return {
    key: error?.name ?? errorKey ?? "",
    type: type,
    message: extractErrorMessage(error, message),
    variables: variables
  };
}

/**
 * Helper to update the tool error state with multiple AnalysisValidityState objects
 * @param {AnalysisToolErrorState} errorState The current error state
 * @param {AnalysisValidityState[]} validityStates The validity states to update the error state with
 * @param {AnalysisValidityState[]} RFxValidityStates The RFx validity states to update the error state with
 * @returns {AnalysisToolErrorState} An updated error state
 */
export function updateToolErrorState(
  errorState: AnalysisToolErrorState,
  validityStates: AnalysisValidityState[],
  RFxValidityStates: RFxValidityState[] = []
): AnalysisToolErrorState {
  const newErrorState: AnalysisToolErrorState = { ...errorState, toolAppErrorState: [] };

  const allStates = [...validityStates, ...RFxValidityStates];
  allStates.forEach((state: AnalysisValidityState | RFxValidityState) => {
    if (state.valid === false) {
      let newToolErrors: AnalysisToolValidationParams[] = [];

      if ("errorKeys" in state) {
        newToolErrors = state.errorKeys.map((errorKey: string | undefined) =>
          convertErrorToAnalysisValidationParams({ errorKey })
        );
      } else if ("errorStrings" in state) {
        newToolErrors = state.errorStrings.map((errorString: string | undefined) =>
          convertErrorToAnalysisValidationParams({ message: errorString })
        );
      }

      newErrorState.toolAppErrorState.push(...newToolErrors);
    }
  });

  // Remove duplicates
  newErrorState.toolAppErrorState = uniqWith(newErrorState.toolAppErrorState, isEqual);

  return newErrorState;
}

/**
 * Returns a validity state object for a given set of errors
 * @param {Error[]} errorList An array of errors
 * @returns {AnalysisValidityState} A validity state object
 */
export function getParameterErrors(errorList: (Error | ArcgisWebAnalysisError)[]): AnalysisParameterError {
  const parameterErrors: AnalysisParameterError = {
    errors: []
  };

  for (const error of errorList) {
    let convertedError: AnalysisValidationParams | undefined = undefined;
    const errorKey = error.name;

    switch (errorKey) {
      case ErrorKeywords.LayerUnavailable:
        const layer = "details" in error ? error.details?.layer : undefined;
        convertedError = convertErrorToAnalysisValidationParams({
          error,
          errorKey,
          variables: {
            title: getServiceName((layer as Url)?.url) ?? ""
          }
        });
        break;
      default:
        break;
    }
    // TODO: Add more cases as needed

    if (convertedError !== undefined) {
      parameterErrors.errors.push(convertedError);
    }
  }

  return parameterErrors;
}
