import { AnalysisToolUIParam } from "analysis-ui-schema";

/**
 * An abstract class to be extended by all classes that perform serialization.
 *
 * This provides a common interface for all serializers, allowing them to be used interchangeably.
 * This interface can later be used to create a generic serializer (or Builder) that can be
 * used to serialize any type of data.
 */
export abstract class AbstractSerializer<Input, Output> {
  /**
   * The main method of the serializer
   * @param {Input} value - the value to be serialized
   * @returns {Output} the serialized value, this should be able to be passed directly to the postProcess method
   */
  abstract serialize(value: Input): Output;

  /**
   * The method that prepares the value for serialization. This is called before the serialize method,
   * and is used to perform any necessary transformations or saving of state.
   *
   * This method's implementation is optional, if not implemented, the provided value will be returned unchanged.
   * @param {Input} value - the value to be serialized
   * @param {AnalysisToolUIParam} _paramConfig - the configuration for the parameter
   * @returns {Input} the prepared value, this should be able to be passed directly to the serialize method
   */
  prepare(value: Input, _paramConfig: AnalysisToolUIParam): Input {
    return value;
  }

  /**
   * The method that performs any necessary post-processing of the serialized value.
   * This is called after the serialize method, and is used to perform any necessary transformations
   * or re-naming of the serialized value. Often this will make use of data saved in the prepare method.
   *
   * This is optional, and if not provided the serialized value will be returned unchanged.
   * @param {Output} value - the serialized value
   * @param {AnalysisToolUIParam} _paramConfig - the configuration for the parameter
   * @returns {Input | Output} the post-processed value to be used as the final serialized value
   */
  postProcess(value: Output, _paramConfig: AnalysisToolUIParam): Input | Output {
    return value;
  }
}
