import esriId from "@arcgis/core/identity/IdentityManager";
import FeatureLayer from "@arcgis/core/layers/FeatureLayer";
import ImageryLayer from "@arcgis/core/layers/ImageryLayer";
import ImageryTileLayer from "@arcgis/core/layers/ImageryTileLayer";
import TileLayer from "@arcgis/core/layers/TileLayer";
import MapView from "@arcgis/core/views/MapView";
import * as geometryJsonUtils from "@arcgis/core/geometry/support/jsonUtils";
import Layer from "@arcgis/core/layers/Layer";
import PortalItem from "@arcgis/core/portal/PortalItem";

import { GeometryType, IFeature, IField } from "@esri/arcgis-rest-types";

import {
  FeatureCollection,
  GPDataType,
  GPFeatureRecordSetLayer,
  GPRasterDataLayer,
  LayerUrlFilter,
  LayerUrlRuleFilter
} from "analysis-ui-schema";

import { isEmptyDataItem } from "../common";
import { ErrorKeywords } from "../errors/interfaces";
import { throwError } from "../t9n/errors";

import { getServiceType, sanitizeUrl } from "./common";
import { isFeatureCollectionJSON, isGroupLayer, isImageryLayer } from "./layer";

function getFeatureCollectionJSON(value: FeatureLayer): FeatureCollection {
  const sourceArray = value.source.toArray();
  const features: IFeature[] = sourceArray.map((graphic: __esri.Graphic) => graphic.toJSON());
  const geometry = sourceArray[0].geometry;
  const gpValue: FeatureCollection = {
    layerDefinition: {
      id: value.id,
      drawingInfo: {
        renderer: value.renderer.toJSON()
      },
      name: value.title
    },
    featureSet: {
      features
    },
    nextObjectId: sourceArray.length,
    showLegend: true
  };
  if (!isEmptyDataItem(value.objectIdField)) {
    gpValue.layerDefinition.objectIdField = value.objectIdField;
  }
  if (!isEmptyDataItem(geometry)) {
    gpValue.layerDefinition.geometryType = geometryJsonUtils.getJsonType(geometry) as GeometryType;
  }
  if (!isEmptyDataItem(value.spatialReference)) {
    gpValue.layerDefinition.spatialReference = value.spatialReference.toJSON();
  }
  if (!isEmptyDataItem(value.fields)) {
    gpValue.layerDefinition.fields = value?.fields.map((field: __esri.Field): IField => {
      const fieldJson: IField = field.toJSON();
      // JSAPI has issue with field.toJSON conversion and sketch layer field lengths.
      // delete length property on OID field and string field should be at-least 255.
      // https://devtopia.esri.com/WebGIS/arcgis-js-api/issues/52165
      // https://devtopia.esri.com/WebGIS/arcgis-js-api/issues/52159
      if (fieldJson.type === "esriFieldTypeOID" && fieldJson.length === -1) {
        delete fieldJson.length;
      }
      if (fieldJson.type === "esriFieldTypeString" && fieldJson.length === -1) {
        fieldJson.length = 255;
      }
      return fieldJson;
    });
  }
  if (!isEmptyDataItem(value.types)) {
    gpValue.layerDefinition.types = value.types.map((type: __esri.FeatureType) => {
      return type.toJSON();
    });
  }
  if (!isEmptyDataItem(value.typeIdField)) {
    gpValue.layerDefinition.typeIdField = value.typeIdField;
  }
  if (!isEmptyDataItem(value.popupTemplate)) {
    gpValue.popupInfo = value.popupTemplate.toJSON();
  }
  return gpValue;
}

/**
 * Returns GPFeatureRecordSetLayer value for a feature layer.
 * @param {FeatureLayer} value The featule layer passed
 * @param {MapView} mapView current MapView instance
 * @returns {GPFeatureRecordSetLayer | undefined} returns the GPFeatureRecordSetLayer object
 */
export function getGPFeatureRecordSetLayerValue(
  value: FeatureLayer,
  mapView?: MapView
): GPFeatureRecordSetLayer | undefined {
  let gpValue: GPFeatureRecordSetLayer | undefined;
  if (isEmptyDataItem(value.url) === false) {
    const credential = esriId.findCredential(value.url);
    gpValue = {
      url: `${value.url}/${value.layerId ?? 0}`
    } as LayerUrlFilter;
    if (credential !== undefined) {
      gpValue.serviceToken = credential.token;
    }
    if (isEmptyDataItem(value.definitionExpression) === false) {
      gpValue.filter = value.definitionExpression;
    }
    if (isEmptyDataItem(value.sourceJSON?.serviceItemId) === false) {
      gpValue.itemId = value.sourceJSON?.serviceItemId;
    }
    // pass time filter when present
    if (
      isEmptyDataItem<__esri.TimeInfo>(value.timeInfo) === false &&
      value.useViewTime === true &&
      isEmptyDataItem<__esri.TimeExtent>(mapView?.timeExtent) === false
    ) {
      let timeFilter = "";
      if (mapView?.timeExtent.start !== undefined) {
        timeFilter += mapView?.timeExtent.start.getTime();
        if (mapView?.timeExtent.end !== undefined) {
          timeFilter += `, ${mapView?.timeExtent.end.getTime()}`;
        }
      } else if (mapView?.timeExtent.end !== undefined) {
        timeFilter += mapView?.timeExtent.end.getTime();
      }
      if (timeFilter.length > 0) {
        gpValue.time = timeFilter;
      }
    }
  } else if (value.source !== undefined) {
    // handle feature collection.
    gpValue = getFeatureCollectionJSON(value);
  }
  return gpValue;
}

/**
 * Loads a list of GPFeatureRecordSetLayers and returns a list of FeatureLayers
 * @param {LayerUrlFilter[]} layers the list of unloaded layers (with urls)
 * @returns {Promise<__esri.FeatureLayer[]> } list of feature layer instances
 */
export async function loadFeatureLayers(layers: LayerUrlFilter[]): Promise<__esri.FeatureLayer[]> {
  const featureLayers = layers?.map((layer: LayerUrlFilter) => {
    return new FeatureLayer({ url: layer.url }).load();
  });
  return Promise.all(featureLayers);
}

/**
 * Returns the name of the service from the layer's url
 * @param {string} url layer url
 * @returns {string} the name of the service
 */
export function getServiceName(url?: string): string {
  let serviceName = "";
  if (url !== undefined && url !== "") {
    const urlParts: string[] = url.split("/");
    const serviceType = getServiceType(url);
    serviceName = urlParts[urlParts.indexOf(serviceType) - 1];
  }

  return serviceName;
}

/**
 * Returns GPFeatureRecordSetLayer value for an imagery layer including token/renderingrull/mosaicrule
 * @param {ImageryLayer} value imagery layer passed
 * @returns {GPRasterDataLayer | undefined} GPRasterDataLayer data value
 */
export function getImageryLayerValue(value: ImageryLayer | ImageryTileLayer): GPRasterDataLayer | undefined {
  let gpValue: LayerUrlRuleFilter | undefined;
  if (isEmptyDataItem(value.url) === false) {
    let url = value.url;
    if (url && url.indexOf("?token=") > 0) {
      url = url.slice(0, url.indexOf("?token="));
    }
    const credential = esriId.findCredential(value.url);
    gpValue = {
      url: `${url}`
    } as LayerUrlRuleFilter;
    if (credential !== undefined) {
      gpValue = {
        url: `${url}?token=${credential.token}`
      } as LayerUrlRuleFilter;
    }
    if (isImageryLayer(value) && value.renderingRule !== undefined && value.renderingRule !== null) {
      gpValue.renderingRule = value.renderingRule.toJSON();
    }
    if (isImageryLayer(value) && value.definitionExpression !== undefined && value.definitionExpression !== null) {
      let mosaicRule = value.mosaicRule ?? __esri.MosaicRule.fromJSON(value.sourceJSON);
      if (mosaicRule !== undefined && mosaicRule !== null) {
        mosaicRule = mosaicRule.clone();
        mosaicRule.where = value.definitionExpression;
        gpValue.mosaicRule = mosaicRule.toJSON();
      }
    } else if (isImageryLayer(value) && value.mosaicRule !== undefined && value.mosaicRule !== null) {
      gpValue.mosaicRule = value.mosaicRule.toJSON();
    }
  }
  return gpValue;
}

/**
 * Returns GPFeatureRecordSetLayer value for a tile layer
 * @param {TileLayer} value tile layer passed
 * @returns {GPRasterDataLayer | undefined} GPRasterDataLayer data value
 */
export function getTileLayerValue(value: TileLayer): GPRasterDataLayer | undefined {
  let gpValue: GPRasterDataLayer | undefined;
  if (isEmptyDataItem(value.url) === false) {
    gpValue = {
      url: `${value.url}`
    } as LayerUrlFilter;
  }
  return gpValue;
}

/**
 * Helper function for getting a layer object using the layer's url
 * @param {string} url the layer's url
 * @returns {Promise<Layer | undefined>} a layer object
 */
async function getLayerObjectFromUrl(url: string): Promise<Layer | undefined> {
  let layer: Layer | undefined = undefined;

  try {
    layer = await Layer.fromArcGISServerUrl({
      url: sanitizeUrl(url)
    });
  } catch (error) {
    await throwError(ErrorKeywords.LayerUnavailable, error);
  }

  return layer;
}

/**
 * Helper function for getting a layer object using the layer's itemId
 * @param {string} itemId the layer's itemId
 * @returns {Promise<Layer | undefined>} a layer object
 */
async function getLayerObjectFromItemId(itemId: string): Promise<Layer | undefined> {
  let layer: Layer | undefined = undefined;

  try {
    layer = await Layer.fromPortalItem({
      portalItem: new PortalItem({
        id: itemId
      })
    });
  } catch (error) {
    await throwError(ErrorKeywords.LayerUnavailable, error);
  }

  return layer;
}

/**
 * Helper function to load/group layer a layer object, and handle errors
 * @param {Layer} layer the layer to load
 * @returns {Promise<Layer>} the loaded layer
 */
export async function loadLayer(layer: Layer): Promise<Layer | undefined> {
  let loadedLayer: Layer | undefined = undefined;

  try {
    loadedLayer = await (isGroupLayer(layer) === true ? layer.loadAll() : layer.load());
  } catch (error) {
    await throwError(ErrorKeywords.LayerLoadFailure, error);
  }

  return loadedLayer;
}

/**
 * This is a helper function that will create a FeatureLayer object from the GPFeatureRecordSetLayer
 * @param {GPDataType} gpResultValue GPFeatureRecordSetLayer
 * @returns {Promise<Layer | undefined>} returns a feature layer instance
 */
export async function getLayerObject(gpResultValue: GPDataType): Promise<Layer | undefined> {
  let layer: Layer | undefined = undefined;

  if (
    typeof gpResultValue === "object" &&
    "itemId" in gpResultValue &&
    isEmptyDataItem(gpResultValue.itemId) === false
  ) {
    // type narrowing isn't working here, so we need to cast itemId to string
    layer = await getLayerObjectFromItemId(gpResultValue.itemId as string);
  } else if (
    typeof gpResultValue === "object" &&
    "url" in gpResultValue &&
    isEmptyDataItem(gpResultValue.url) === false
  ) {
    layer = await getLayerObjectFromUrl(gpResultValue.url);
  }
  if (layer !== undefined) {
    layer = await loadLayer(layer);
  }

  return layer;
}

/**
 * This is a helper function that will create a FeatureLayer object from the GPFeatureRecordSetLayer
 * @param {GPDataType} gpInputValue the job's layer value
 * @returns {Promise<Layer | undefined>} a layer object
 */
export async function getInputLayerObject(gpInputValue: GPDataType): Promise<Layer | undefined> {
  let layer: Layer | undefined = undefined;
  const errors = [];

  const { url, itemId } = gpInputValue as { url: string; itemId: string };

  if (isEmptyDataItem(url) === false) {
    try {
      layer = await getLayerObjectFromUrl(url);
    } catch (error) {
      // if error is thrown, try to load layer from itemId, but save error
      errors.push(error);
    }
  }

  // if the layer is still undefined, try to load layer from itemId
  if (isEmptyDataItem(itemId) === false && isEmptyDataItem(layer) === true) {
    try {
      layer = await getLayerObjectFromItemId(itemId);
    } catch (error) {
      // unable to load layer from url or itemId, save error to throw later
      errors.push(error);
    }
  }

  // If this code is reached the sketch is no longer in the webmap
  // Create an empty feature layer with a load error to pass through the warning to the UI
  if (isFeatureCollectionJSON(gpInputValue as FeatureCollection)) {
    layer = new FeatureLayer({
      title: (gpInputValue as FeatureCollection)?.layerDefinition.name ?? ""
    });
  }

  if (isEmptyDataItem(layer) === true && errors.length > 0) {
    // if layer is still undefined and attempts to load layer from url and itemId failed, throw error
    await throwError(ErrorKeywords.LayerUnavailable, errors);
  }

  return layer;
}
