import Point from "@arcgis/core/geometry/Point";
import Polyline from "@arcgis/core/geometry/Polyline";
import Polygon from "@arcgis/core/geometry/Polygon";
import esriId from "@arcgis/core/identity/IdentityManager";
import * as reactiveUtils from "@arcgis/core/core/reactiveUtils";
import Query from "@arcgis/core/rest/support/Query";

import { isEmptyDataItem, request } from "../common";
import { abortPendingQueries, queryFeatures } from "../query";
import { fromCache, inCache } from "../storage";

interface GetEnrichCountryRuleParams {
  inputLayer?: __esri.FeatureLayer;
  geoenrichmentUrl: string;
  mapView?: __esri.MapView;
}

/**
 * returns a center point for a geometry ( polygon or polyline)
 * @param geometry the input geometry passed to find its center point
 * @returns {Point} the center point of the geometry
 */
function getCenter(geometry: __esri.Geometry): Point {
  const extent = geometry.extent;
  return new Point({
    x: (extent.xmin + extent.xmax) / 2,
    y: (extent.ymin + extent.ymax) / 2,
    spatialReference: extent.spatialReference
  });
}

/**
 * returns a center point for a geometry ( point or polygon or polyline)
 * @param {Point | Polygon | Polyline} geometry the input geometry passed to find its center point
 * @returns {Point} the center point of the geometry
 */
function getGeometryCenter(geometry: Point | Polygon | Polyline): Point {
  let center: Point | undefined;
  switch (geometry.type) {
    case "point":
      center = geometry;
      break;
    case "polyline":
      // Pick one segment from the geometry and obtain its center
      const path = geometry.paths[0];
      const polyline = new Polyline({ spatialReference: geometry.spatialReference });
      polyline.addPath(path);
      center = getCenter(polyline);
      break;
    case "polygon":
      if (isEmptyDataItem(geometry.centroid) === false) {
        // use centroid for polygon when present
        center = getGeometryCenter(geometry.centroid);
      } else {
        // Pick one ring from the geometry and obtain its center
        const ring = geometry.rings[0];
        const polygon = new Polygon({ spatialReference: geometry.spatialReference });
        polygon.addRing(ring);
        center = getCenter(polygon);
      }
      break;
  }
  return center;
}

/**
 * returns the center point for an input feature layer,
 * here when layerView is not available or no features are in view we query the server.
 * and find its geometry center to get the lat/long of the center for enrich task to use.
 * @param {__esri.FeatureLayer} inputLayer feature layer to be queried to get its sample point
 * @returns {Promise<Point | undefined>} returns the center point for finding the country for enrich.
 */
async function getCenterPointUsingServerQuery(inputLayer: __esri.FeatureLayer): Promise<Point> {
  let point: Point | undefined = undefined;

  // query the server
  const query = new Query();
  query.returnGeometry = true;
  query.where = "1=1";
  // we want to fetch only 5 features and find the mid-point , use it to determine the country
  query.num = 5;
  // setting maxAllowableOffset > 0 to simplify the returned geometries so it improves performance
  query.maxAllowableOffset = 0.25;
  query.outFields = ["*"];
  // cache the results of same query
  query.cacheHint = true;
  // for polygons returning centroid so we can use that to determine the center
  query.returnCentroid = inputLayer?.geometryType === "polygon";
  abortPendingQueries(inputLayer.id);
  const features = (await queryFeatures({
    layer: inputLayer,
    query,
    abortKey: inputLayer.id
  })) as __esri.Graphic[];
  const featureIndex = Math.round(features.length / 2);
  const sampleFeature = (features?.[featureIndex]?.geometry as Point | Polygon | Polyline) ?? undefined;
  if (sampleFeature !== undefined) {
    point = getGeometryCenter(sampleFeature);
  } else {
    point = getGeometryCenter(inputLayer.fullExtent.center);
  }
  return point;
}

/**
 * returns the center point for an input feature layer,
 * We could use fullExtent to find the center of feature layer which is not reliable
 * so we query the feature layer view to get the client-side geometries, pick the middle feature
 * when layerView is not available or no features are in view we query the server.
 * and find its geometry center to get the lat/long of the center for enrich task to use.
 * @param {GetEnrichCountryRuleParams} params parameters needed for the function
 * @returns {Promise<Point | undefined>} returns the center point for finding the country for enrich.
 */
async function getCenterPoint(params: GetEnrichCountryRuleParams): Promise<Point | undefined> {
  const { inputLayer, mapView } = params;
  let point: Point | undefined = undefined;
  if (inputLayer !== undefined && mapView !== undefined) {
    // Check for the first time layerView.updating becomes false. Then query for
    // features that are visible within the view associated with the layer view.
    let layerView: __esri.FeatureLayerView | undefined;
    let sampleFeature: Point | Polygon | Polyline | undefined;
    let featureIndex = 0;
    let query: Query | undefined;
    try {
      layerView = await mapView.whenLayerView(inputLayer);
      // when layer view is available we use the view to query
      if (layerView !== undefined) {
        await reactiveUtils.whenOnce(() => layerView?.updating === false);
        query = layerView.createQuery();
        const resultFeatureSet = await layerView.queryFeatures(query);
        featureIndex = Math.floor(resultFeatureSet.features?.length / 2) ?? 0;

        // we use the first feature from the list of features in the layer , when not present query the server ,
        // otherwise we use the full extent
        sampleFeature =
          (resultFeatureSet.features?.[featureIndex]?.geometry as Point | Polygon | Polyline) ?? undefined;
      }
      // if sampleFeature is undefined either when there are no features in extent
      if (sampleFeature === undefined) {
        point = await getCenterPointUsingServerQuery(inputLayer);
      } else {
        point = getGeometryCenter(sampleFeature);
      }
    } catch (e) {
      // if layerView is undefined like when we select layer from item browser with adding layer to map
      point = await getCenterPointUsingServerQuery(inputLayer);
    }
  }
  return point;
}

const CountryNotFound = "NOT_FOUND";

/**
 *  Returns the default country for an input layer by making an enrich an async request using first input feature geometry.
 * @param {GetEnrichCountryRuleParams} params input params to this function
 * @returns {string | undefined } returns the country Id when found otherwise returns undefined
 */
async function fetchEnrichmentCountry({ params }: { params: GetEnrichCountryRuleParams }): Promise<string> {
  const { geoenrichmentUrl, inputLayer } = params;
  let country: string = CountryNotFound;
  const centerPoint = await getCenterPoint(params);
  if (centerPoint !== undefined && inputLayer !== undefined) {
    const url = `${geoenrichmentUrl}/Geoenrichment/Enrich?f=json`;
    // const countryRule: AnalysisToolUIRule = [];
    const credential = esriId.findCredential(url);
    let token;
    if (credential !== undefined) {
      token = credential.token;
    }
    try {
      const options = {
        query: {
          token,
          f: "json",
          studyAreas: JSON.stringify([{ geometry: centerPoint.toJSON() }]),
          analysisVariables: JSON.stringify(["GlobalIntersect.*"]),
          inSr: JSON.stringify(inputLayer.spatialReference.toJSON())
        }
      };
      const response = await request(`${geoenrichmentUrl}/Geoenrichment/Enrich`, options);
      // it mimics a GP result but nested this way to get the GPResult.value
      const enrichResult = response.data.results[0].value;
      const features: __esri.Graphic[] = enrichResult.FeatureSet?.[0]?.features ?? [];
      const countriesList = features.map((feature) => feature.attributes.sourceCountry);
      // there may be duplicate countries so using set.
      const countries = Array.from(new Set(countriesList));
      country = countries?.[0] ?? CountryNotFound;
    } catch (err) {
      // no-op
      console.error("Failed to get enrich info", err);
    }
  }
  return country;
}

/**
 *  Returns the default country for an input layer by making an enrich an async request using first input feature geometry.
 * @param {GetEnrichCountryRuleParams} params input params to this function
 * @returns {string | undefined } returns the country Id when found otherwise returns undefined
 */
export async function getCountry(params: GetEnrichCountryRuleParams): Promise<string | undefined> {
  let country: string | undefined;
  const { inputLayer } = params;
  if (inputLayer !== undefined) {
    // check the cache first
    country = inCache("enrichmentCountry", inputLayer?.id) as string | undefined;
    if (country === undefined) {
      country = (await fromCache(() => fetchEnrichmentCountry({ params }), "enrichmentCountry", inputLayer?.id)) as
        | string
        | undefined;
    }
  }
  if (country === CountryNotFound) {
    country = undefined;
  }
  return country;
}
