import * as urlUtils from "@arcgis/core/core/urlUtils";

/**
 * Returns the type of the server from the layer's url
 * @param {string} url layer url
 * @returns {string} the type  of the server
 */
export function getServiceType(url?: string): string {
  let serviceType = "";
  if (url !== undefined && url !== "") {
    const urlParts: string[] = url.split("/");

    const services = urlParts.filter((part: string) => {
      return new RegExp("[A-Z]{1}[a-z]+Server").test(part) === true;
    });
    serviceType = services[services.length - 1];
  }

  return serviceType;
}

/**
 * Helper function for sanitizeTokensFromJobParams - splits the url into parts and removes the ?token
 * @param {string} url url to be sanitized
 * @returns {string} sanitized string
 */
export function sanitizeUrl(url: string): string {
  return urlUtils.urlToObject(url).path;
}
